import React, { Component } from 'react';
import { Table, Select, Modal, Radio, Input } from "antd";
import { connect } from "react-redux";
import { actionCreators } from './store';
import { Link } from "react-router-dom";
import Button from '../../../common/components/Button';
import moment from 'moment';

const { Option } = Select;
const { Search } = Input;

class Kefu extends Component {

  state = {
    messageType: "请选择消息类型"
  };

  UNSAFE_componentWillMount () {
    this.props.getKefuMessageList();
    this.props.getAllGroup();
    this.props.changePage(this.props.currentPage);
  }

  render() {
    const { kefuMessageList, copyMessage, deleteMessage, sendKefuMessage, messageType, handleChangeSelect, allGroup, messageGroup, selectedMessages, changeSelectedMessages, updateMoreGroup, updateGroupModal, openUpdateGroupModal, changeGroup, cancelUpdateGroup, group, changePage, currentPage, searchName, changeSearchName, searchByName, clearCondition }  = this.props;
    const kefuMessageHeader = [
      {
        key: "remarks",
        title: "消息名称",
        dataIndex: "remarks",
        align: "center",
        render: text => !text ? "请点击修改，编辑消息名称" : text
      },
      {
        key: "group",
        title: "分组",
        dataIndex: "group",
        align: "center",
        width: 80
      },
      {
        key: "delay",
        title: "延时时间",
        align: "center",
        width: 100,
        render: (text, record) => record.task ? (record.isHour ? record.delay / 60 + "小时" : record.delay + "分钟") : "——"
      },
      {
        key: "timing_time",
        title: "定时时间",
        align: "center",
        sorter: (a, b) => (new Date(a.timing_time)).getTime() - (new Date(b.timing_time)).getTime(),
        render: (text, record) => record.is_timing ? record.time : "——",
      },
      {
        key: "daily_time",
        dataIndex: "daily_time",
        title: "按时发送",
        align: "center",
        sorter: (a, b) => b.daily_time - a.daily_time,
        render: (text, record) => record.is_daily ? moment(text += new Date(new Date().toLocaleDateString()).getTime()).format("HH:mm:ss") : "——",
      },
      {
        key: "gonghaoList",
        dataIndex: "gonghaoList",
        title: "已选公号 (最多显示4个)",
        align: "center",
        width: 120,
        render: text => text.slice(0, 4).map(item => <p key={item._id}>{item.nick_name}</p>)
      },
      {
        key: "sex",
        title: "群发粉丝",
        align: "center",
        dataIndex: "sex",
        width: 80,
        render: text => text === "0" ? "未知粉丝" : text === "1"  ? "男性粉丝" : text === "2" ? "女性粉丝" : "全部粉丝"
      },
      {
        key: "action_type",
        title: "互动行为",
        align: "center",
        dataIndex: "action_type",
        width: 80,
        render: text => text === 1 ? "用户关注" : "用户互动"
      },
      {
        title: "Action",
        align: "center",
        width: 280,
        render: (text, record, index) => (
          <div>
            <Button background="success" onClick={() => copyMessage({...record})} buttonContent="复制" />
            <Button buttonContent={<Link to={{pathname: "./update", query: {...record}}}>修改</Link>} />
            <Button disabled={record.is_timing || record.delay || record.is_daily} background={(record.is_timing || record.delay || record.is_daily) ? "info" : "warning"} onClick={() => sendKefuMessage(record._id, record.tagId)} buttonContent="发送消息" />
            <Button background="danger" onClick={() => deleteMessage(record._id, index)} buttonContent="删除" />
          </div>
        )
      }
    ];
    const rowSelection = {
      selectedRowKeys: selectedMessages,
      onChange: changeSelectedMessages
    };
    const pagination = {
      showQuickJumper: true,
      hideOnSinglePage: true,
      onChange: changePage,
      current: currentPage
    };
    return (
    	<div>
        <div className="container">
          <Search value={searchName} style={{width: 240, marginRight: 5}} placeholder="请输入公号名称" enterButton="查询" onChange={changeSearchName} onSearch={searchByName} />
          <Select value={messageType} onChange={value => handleChangeSelect(value, "messageType")} style={{marginRight: 5}}>
            <Option value={4}>全部</Option>
            <Option value={0}>手动</Option>
            <Option value={1}>延时</Option>
            <Option value={2}>定时</Option>
            <Option value={3}>按时</Option>
          </Select>
          <Select style={{width: 100}} value={messageGroup} onChange={value => handleChangeSelect(value, "messageGroup")}>
            <Option value="全部分组">全部分组</Option>
            <Option value="未分组">未分组</Option>
            {allGroup.map(item => <Option key={item._id} value={item.group}>{item.group}</Option>)}
          </Select>
          <Button size="default" background="warning" buttonContent="清空所有条件" ml={5} onClick={clearCondition} />
          <Button size="default" ml={5} background="success" buttonContent="批量修改分组" onClick={openUpdateGroupModal} />
          <Button size="default" mb={20} buttonContent={<Link to="./create">新建客服消息</Link>} />
          <Table size="middle" rowKey={record => record._id} columns={kefuMessageHeader} dataSource={kefuMessageList} rowSelection={rowSelection} pagination={pagination} />
        </div>
        <Modal title="请选择分组" visible={updateGroupModal} onCancel={cancelUpdateGroup} onOk={() => updateMoreGroup(selectedMessages, group)}>
          <Radio.Group onChange={changeGroup} value={group}>
            {allGroup.map(item => <Radio key={item._id} value={item.group}>{item.group}</Radio>)}
          </Radio.Group>
        </Modal>
      </div>
    )
  }
}

const mapState = state => ({
  kefuMessageList: state.message.kefu.kefuMessageList,
  messageType: state.message.kefu.messageType,
  allGroup: state.message.kefu.allGroup,
  messageGroup: state.message.kefu.messageGroup,
  selectedMessages: state.message.kefu.selectedMessages,
  updateGroupModal: state.message.kefu.updateGroupModal,
  group: state.message.kefu.group,
  currentPage: state.message.kefu.currentPage,
  searchName: state.message.kefu.searchName
});

const mapDispatch = dispatch => ({
  getKefuMessageList() {
    dispatch(actionCreators.getKefuMessageList())
  },
  getAllGroup() {
    dispatch(actionCreators.getAllGroup())
  },
  copyMessage(record) {
    dispatch(actionCreators.createKefuMessage(record))
  },
  deleteMessage(id, index) {
    dispatch(actionCreators.deleteMessage(id, index))
  },
  sendKefuMessage(id, tagId) {
    dispatch(actionCreators.sendKefuMessage(id, tagId))
  },
  handleChangeSelect(value, name) {
    dispatch(actionCreators.handleChangeSelect(value, name))
  },
  changeSelectedMessages(selectedMessages) {
    dispatch(actionCreators.changeSelectedMessages(selectedMessages))
  },
  updateMoreGroup(selectedMessages, group) {
    dispatch(actionCreators.updateMoreGroup(selectedMessages, group))
  },
  openUpdateGroupModal() {
    dispatch(actionCreators.openUpdateGroupModal())
  },
  changeGroup(e) {
    dispatch(actionCreators.changeGroup(e.target.value))
  },
  cancelUpdateGroup() {
    dispatch(actionCreators.cancelUpdateGroup())
  },
  changePage(page) {
    dispatch(actionCreators.changePage(page))
  },
  changeSearchName(e) {
    dispatch(actionCreators.changeSearchName(e.target.value))
  },
  searchByName() {
    dispatch(actionCreators.searchByName())
  },
  clearCondition() {
    dispatch(actionCreators.clearCondition())
  }
});

export default connect(mapState, mapDispatch)(Kefu);