import React from 'react';
import {Spin} from 'antd';

const loadingWrapper = {
  width: "100%",
  textAlign: "center",
  background: "rgba(241, 244, 245, .7)",
  position: "absolute",
  padding: "200px 0",
  top: 0,
  left: 0,
  zIndex: 1,
};

const Loading = props => <div style={loadingWrapper}><Spin spinning={props.spinning}/></div>

export default Loading;