import React, {PureComponent} from 'react';
import {ConfigProvider, message, Modal, Radio, Switch, DatePicker, TimePicker, Checkbox, Row, Col, Input} from "antd";
import zhCN from 'antd/es/locale/zh_CN';
import moment from "moment";
import 'moment/locale/zh-cn';
import {connect} from "react-redux";
import { actionCreators } from '../store';
import GraphicMessage from '../components/GraphicMessage';

import axios from 'axios';

class groupMessage extends PureComponent {

  constructor(props) {
    super(props);
    this.state = {
      syncModal: false,
      checkedList: [],
      indeterminate: false,
      checkAll: false,
      messageInfo: {},
      code: props.code,
      gonghaoList: []
    };
  }

  componentDidMount() {
    const { code } = this.state;
    this.props.getMessageList(code);
    this.props.getTagList(code);
    this.props.getGroupGonghao();
    this.getGonghaoList();
  }

  UNSAFE_componentWillReceiveProps(nextProps, nextContext) {
    if(this.props.code !== nextProps.code) {
      const { code } = nextProps;
      this.props.getMessageList(code);
    }
  }

  getGonghaoList = () => {
    axios.get('/conf')
      .then(res => {
        const { data, code } = res.data;
        if (code === 1) {
          this.setState({gonghaoList: data})
        }
      })
  };

  render() {
    const { messageList, openModal, sendModal, sendMessage, sendInfo, tagList, closeModal, changeTagId, sendTimingModal, sendTimingMessage, sendTimingInfo, openTimingModal, changeTimingState, copy, changeTime, deleteMaterial, isComplete, openUpdateMaterialModal, closeUpdateMaterialModal, updateMaterialModal, updateMaterial, updateInfo, changeContentSourceUrl }  = this.props;
    const { code, gonghaoList } = this.state;
    let selectGonghaoList = gonghaoList.filter(item => item.code !== code);
    const selectTagRadio = tagList.map(item => (
      <Radio key={item._id} value={item.id}>{item.name}</Radio>
    ));
    return (
      <ConfigProvider locale={zhCN}>
        <div className="container" style={{padding: 0}}>
          <GraphicMessage messageList={messageList} openSyncModal={message => this.setState({syncModal: true, messageInfo: message})} openModal={openModal} openTimingModal={openTimingModal} copy={copy} deleteMaterial={deleteMaterial} isComplete={isComplete} openUpdateMaterialModal={openUpdateMaterialModal} />
        </div>
        <Modal
          title="请选择要发送的标签"
          visible={sendModal}
          onOk={() => sendMessage(sendInfo)}
          onCancel={closeModal}
        >
          <Radio.Group value={sendInfo.tagId} onChange={e => changeTagId(e, false)}>
            {selectTagRadio}
          </Radio.Group>
        </Modal>
        <Modal
          title="创建定时发送任务？"
          visible={sendTimingModal}
          onOk={() => sendTimingMessage(sendTimingInfo, code)}
          onCancel={closeModal}
        >
          <label htmlFor="tagId">
            标签：
            <Radio.Group id="tagId" value={sendTimingInfo.tagId} onChange={e => changeTagId(e, true)}>
              {selectTagRadio}
            </Radio.Group>
          </label>
          <label style={{display: "block", margin: "20px 0"}} htmlFor="isTiming">
            是否定时：
            <Switch checked={sendTimingInfo.isTiming} onChange={changeTimingState} />
          </label>
          <label htmlFor="timing" style={{display: sendTimingInfo.isTiming ? "block" : "none"}}>
            时间选择：
            <DatePicker
              value={moment(new Date(sendTimingInfo.timing), 'YYYY-MM-DD')}
              onChange={(time, timeString) => changeTime(timeString, "date")}
            />
            <TimePicker
              value={moment(new Date(sendTimingInfo.timing), 'HH-MM-SS')}
              onChange={(time, timeString) => changeTime(timeString, "time")}
            />
          </label>
        </Modal>
        <Modal width={640} title="请选择需要同步素材的公号：" visible={this.state.syncModal} onOk={this.syncMaterial} onCancel={this.closeSyncModal}>
          <div style={{ borderBottom: '1px solid #E9E9E9', paddingBottom: 10, marginBottom: 10 }}>
            <Checkbox
              indeterminate={this.state.indeterminate}
              onChange={this.onCheckAllChange}
              checked={this.state.checkAll}
            >全选</Checkbox>
          </div>
          <Checkbox.Group style={{width: "100%"}} onChange={this.onChange} value={this.state.checkedList}>
            <Row>
              {selectGonghaoList.map(item => <Col span={6} key={item._id}>
                <Checkbox value={item.code}>{item.nick_name}</Checkbox>
              </Col>)}
            </Row>
          </Checkbox.Group>
        </Modal>
        <Modal visible={updateMaterialModal} onCancel={closeUpdateMaterialModal} onOk={() => updateMaterial(updateInfo, code)} title="修改原文链接">
          <Input value={updateInfo.articles.content_source_url} placeholder="请输入新的原文链接" onChange={changeContentSourceUrl} />
        </Modal>
      </ConfigProvider>
    )
  }
  componentWillUnmount() {
    this.props.resetState()
  }

  syncMaterial = () => {
    const { messageInfo, checkedList: codes } = this.state;
    Modal.confirm({
      title: "确定同步这条素材吗？",
      onOk: () => {
        axios.post("/material/syncMaterial", {codes, docs: [messageInfo]})
          .then(res => {
            const {code, msg } = res.data;
            if(code === 1) {
              message.success(msg);
              this.closeSyncModal();
            } else {
              message.error(msg)
            }
          })
      }
    })
  };

  closeSyncModal = () => {
    this.setState({
      syncModal: false,
      checkedList: [],
      indeterminate: false,
      checkAll: false,
      messageInfo: {}
    })
  };

  onCheckAllChange = e => {
    const { code, gonghaoList } = this.state;
    const plainOptions = gonghaoList.filter(item => item.code !== code);
    const codes = plainOptions.map(item => item.code);
    this.setState({
      checkedList: e.target.checked ? codes : [],
      indeterminate: false,
      checkAll: e.target.checked,
    });
  };

  onChange = checkedList => {
    const { code, gonghaoList } = this.state;
    const plainOptions = gonghaoList.filter(item => item.code !== code);
    const codes = plainOptions.map(item => item.code);
    this.setState({
      checkedList,
      indeterminate: !!checkedList.length && checkedList.length < codes.length,
      checkAll: checkedList.length === codes.length,
    });
  };
}


const mapState = state => ({
  tagList: state.message.group.tagList,
  messageList: state.message.group.messageList,
  sendInfo: state.message.group.sendInfo,
  sendTimingInfo: state.message.group.sendTimingInfo,
  sendModal: state.message.group.sendModal,
  sendTimingModal: state.message.group.sendTimingModal,
  gonghaoList: state.message.group.gonghaoList,
  isComplete: state.message.group.isComplete,
  updateMaterialModal: state.message.group.updateMaterialModal,
  updateInfo: state.message.group.updateInfo,
});

const mapDispatch = dispatch => ({
  getTagList(code) {
    dispatch(actionCreators.getTagList(code))
  },
  getMessageList(code) {
    dispatch(actionCreators.getMessageList(code))
  },
  openModal(record) {
    dispatch(actionCreators.openModal(record));
  },
  sendMessage(sendInfo) {
    dispatch(actionCreators.sendMessage(sendInfo));
  },
  changeTagId(e, isTiming) {
    dispatch(actionCreators.changeTagId(e.target.value, isTiming));
  },
  openTimingModal(record) {
    dispatch(actionCreators.openTimingModal(record));
  },
  closeModal() {
    dispatch(actionCreators.closeModal());
  },
  sendTimingMessage(sendTimingInfo, code) {
    dispatch(actionCreators.sendTimingMessage(sendTimingInfo, code));
  },
  changeTimingState(value) {
    dispatch(actionCreators.changeTimingState(value));
  },
  changeTime(time, type) {
    dispatch(actionCreators.changeTime(time, type));
  },
  copy(id) {
    dispatch(actionCreators.copy(id));
  },
  deleteMaterial(id, index) {
    dispatch(actionCreators.deleteMaterial(id, index));
  },
  resetState() {
    dispatch(actionCreators.resetState());
  },
  getGroupGonghao() {
    dispatch(actionCreators.getGroupGonghao());
  },
  openUpdateMaterialModal(media_id, index, articles, id) {
    dispatch(actionCreators.openUpdateMaterialModal({media_id, index, articles, id}));
  },
  closeUpdateMaterialModal() {
    dispatch(actionCreators.closeUpdateMaterialModal());
  },
  updateMaterial(updateInfo, code) {
    dispatch(actionCreators.updateMaterial(updateInfo, code));
  },
  changeContentSourceUrl(e) {
    let content_source_url = e.target.value;
    dispatch(actionCreators.changeContentSourceUrl(content_source_url));
  }
});

export default connect(mapState, mapDispatch)(groupMessage);