export const updateMoreGroup = (selectedMessages, group) => {
  return dispatch => {
    axios.put('/message/updateGroup', {selectedMessages, group})
      .then(res => {
        const { code, msg } = res.data;
        if(code === 1) {
          message.success(msg);
          dispatch(getKefuMessageList());
          dispatch({type: constants.UPDATE_MORE_GROUP});
          dispatch(cancelUpdateGroup());
        } else {
          message.error(msg)
        }
      })
  }
};

