export const handleGroupInputConfirm = group => {
  return dispatch => {
    if(group) {
      axios.post('/messageGroup', {group})
        .then(res => {
          const {code, msg, data} = res.data;
          if (code === 1) {
            message.success(msg);
            dispatch({type: constants.HANDLE_GROUP_INPUT_CONFIRM, data})
          } else {
            message.error(msg);
          }
        })
    } else {
      dispatch({type: constants.HANDLE_GROUP_INPUT_CONFIRM, data: {}})
    }
  }
};

export const handleClose = (_id, index) => {
  return dispatch => {
    Modal.confirm({
      title: "确定删除这个分组吗？",
      onOk: () => {
        axios.delete("/messageGroup", {params: {_id}})
          .then(res => {
            const {code, msg} = res.data;
            if(code === 1) {
              message.success(msg);
              dispatch({type: constants.DELETE_GROUP, index})
            } else {
              message.error(msg)
            }
          })
      }
    })
  }
};

