export const submitMessage = (info, history, allCodes) => {
  return dispatch => {
    const LocalDate = new Date(new Date().toLocaleDateString()).getTime();
    info.gonghaoList = allCodes.filter(item => info.codes.indexOf(item.code) > -1);
    !info.img && info.contents.length > 0 && (info.img = info.contents[0].picurl);
    info.channel === 1 ? info.task = true : info.task = false;
    info.channel === 2 ? info.is_timing = true : info.is_timing = false;
    if(info.channel !== 1) {
      info.delay = null;
    }
    if(info.channel === 3) {
      info.is_daily = true;
      if(info.daily_time._d) {
        info.daily_time = new Date(info.daily_time._d).getTime() - LocalDate
      } else {
        info.daily_time = new Date(info.daily_time).getTime() - LocalDate
      }
    } else {
      info.is_daily = false;
      info.daily_time = 0;
    }
    Modal.confirm({
      title: "确定提交吗？",
      onOk: () => {
        let url = "/message/create";
        if(history.location.query) {
          url = "/message/update";
          info.id = info._id
        }
        axios.post(url, {...info})
          .then(res => {
            if (res.data.success) {
              message.info(res.data.success);
              history.goBack();
              dispatch(resetState());
            } else {
              message.info(res.data.err);
            }
          })
      }
    })
  }
};

export const initKefuMessageInfo = query => {
  return dispatch => {
    if (query.tagId) {
      axios.get("/tag/get_name", { params: { tagId: query.tagId } })
        .then(res => {
          query.tagName = res.data.data.name;
          const action = {
            type: constants.INIT_KEFU_MESSAGE_INFO,
            query
          };
          dispatch(action);
        });
    } else {
      const action = {
        type: constants.INIT_KEFU_MESSAGE_INFO,
        query
      };
      dispatch(action);
    }

  }
};

