export const preview = ({codes, type, contents, img_path}, openid) => {
  return dispatch => {
    axios.post('/message/preview', {codes, type, contents, img_path, openid})
      .then(res => {
        const { code, msg } = res.data;
        if(code === 1) {
          message.success(msg);
        } else {
          message.warning(msg);
        }
        dispatch(closePreviewModal());
      })
  }
};

