const backTop = () => {
  return new Promise((resolve, reject) => {
    let timer;
    timer = setInterval(function () {
      let osTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      let ispeed = Math.floor(-osTop / 1.2);
      document.documentElement.scrollTop = document.body.scrollTop =
        osTop + ispeed;
      if (osTop === 0) {
        clearInterval(timer);
      }
    }, 30);
    resolve(null)
  })
};

export default backTop;