import React, { Component } from 'react';
import {Modal, Form, Input, Radio, Switch, DatePicker, Button, message} from "antd";
import axios from 'axios';

import moment from 'moment';
import * as constants from "../../store/constants";

class EditTextMaterialModal extends Component {

  constructor(props) {
    super(props);
    this.openOpenidModal = this.openOpenidModal.bind(this);
    this.prevTextMaterial = this.prevTextMaterial.bind(this);
    this.cancelPrev = this.cancelPrev.bind(this);
    this.submitForm = this.submitForm.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  state = {
    openidModalVisible: false,
    tagList: []
  };

  openOpenidModal() {
    this.setState({openidModalVisible: true})
  }

  prevTextMaterial() {
    const { form: {getFieldsValue}, code } = this.props;
    let {openid, content} = getFieldsValue();
    axios.post("/textMaterial/preview", {code, openid, content})
      .then(res => {
        const {code, msg} = res.data;
        code === 1 && message.success(msg)
      });
    this.cancelPrev()
  }

  cancelPrev() {
    const { form: {setFieldsValue} } = this.props;
    setFieldsValue({openid: ""});
    this.setState({openidModalVisible: false})
  }

  submitForm() {
    const { form: {getFieldsValue} } = this.props;
    this.props.submitTextMaterial(getFieldsValue())
  }

  closeModal() {
    const { form: {resetFields} } = this.props;
    resetFields();
    this.props.cancelEditMaterial()
  }

  componentDidMount() {
    const { code } = this.props;
    axios.get("/material/tag", {params: {code}})
      .then(res => {
        this.setState({tagList: res.data.data});
      });
  }

  render() {
    const { form: {getFieldDecorator, getFieldValue}, editMaterialModalVisible, initMaterial = {} } = this.props;
    const { openidModalVisible, tagList } = this.state;
    return (
      <Modal
        visible={editMaterialModalVisible}
        title="编辑文本素材"
        footer={
          <div>
            <Button onClick={this.openOpenidModal}>发送预览</Button>
            <Button onClick={this.submitForm}>提交</Button>
          </div>
        }
        onCancel={this.closeModal}
      >
        <Form labelCol={{span: 4}} wrapperCol={{span: 20, offset: 0}}>
          <Form.Item label="文本素材">
            {getFieldDecorator('content', {
              initialValue: initMaterial.content || "",
              rules: [{ required: true, message: 'Please input your content!' }]
            })(<Input.TextArea rows={4} placeholder="请输入文本素材内容" />)}
          </Form.Item>
          <Form.Item label="粉丝属性">
            {getFieldDecorator('tagId', {
              initialValue: initMaterial.tagId + "" || "",
              rules: [{required: true}]
            })(<Radio.Group>
              {tagList.map(item =>
                <Radio key={item._id} value={item.sex}>{item.name}</Radio>
              )}
            </Radio.Group>)}
          </Form.Item>
          <Form.Item label="是否定时">
            {getFieldDecorator('is_timing', {
              valuePropName: 'checked',
              initialValue: initMaterial.is_timing || false,
            })(<Switch />)}
          </Form.Item>
          {getFieldValue("is_timing") ? <Form.Item label="发送时间">
            {getFieldDecorator('timing_time', {initialValue: initMaterial.is_timing ? moment(initMaterial.timing_time) : null})(<DatePicker showTime={{format: "HH:mm"}} format="YYYY-MM-DD HH:mm" placeholder="请选择定时发送时间" />)}
          </Form.Item> : ""}
        </Form>
        <Modal visible={openidModalVisible} title="发送预览消息" onOk={this.prevTextMaterial} onCancel={this.cancelPrev}>
          {getFieldDecorator('openid', {
            rules: [{ required: true, message: 'Please input your openid!' }],
            initialValue: ""
          })(<Input placeholder="请输入openid" />)}
        </Modal>
      </Modal>
    )
  }
}

export default Form.create()(EditTextMaterialModal);