import React, { Component } from 'react';
import { Layout, List, Input, Card, Table, Button, Modal, Radio, message, Icon } from 'antd'
import axios from 'axios';

const { Sider, Content } = Layout;
const { Search } = Input;


class GonghaoGroup extends Component {
  columns = [
    {
      key: "nick_name",
      dataIndex: "nick_name",
      title: "公号名称",
      align: "center",
      width: 200
    }, {
      key: "group",
      dataIndex: "group",
      title: "分组",
      align: "center"
    }, {
      key: "action",
      title: "操作",
      align: "center",
      width: 100,
      render: (text, record, index) => <Button type="primary" size="small" onClick={() => this.setState({gonghaoModal: true, gonghao_id: record._id, gonghao_index: index})}>修改分组</Button>
    }
  ];

  state = {
    gonghaoTagList: [],
    gonghaoModal: false,
    editGroupModalVisible: false,
    group: "",
    gonghaoList: [],
    group_id: "",
    group_index: -1,
    gonghao_id: "",
    gonghao_index: -1,
    selectGroup: "",
    selectedRowKeys: []
  };

  componentDidMount() {
    this.getGonghaoTagList();
    this.getGroupGonghao();
  }

  getGonghaoTagList = () => {
    axios.get("/gonghaoTag")
      .then(res => {
        if(res.data.code === 1) {
          const { data: gonghaoTagList } = res.data;
          this.setState({gonghaoTagList})
        }
      })
  };

  getGroupGonghao = group => {
    axios.get('/conf/group', { params: {group} })
      .then(res => {
        let { data: gonghaoList } = res.data;
        this.setState({gonghaoList})
        gonghaoList.length === 0 && message.warning("该分组中还没有数据，请添加")
      })
  };

  openEditGroupModal = () => {
    this.setState({editGroupModalVisible: true})
  };

  closeEditGroupModal = () => {
    this.setState({editGroupModalVisible: false, group: ""})
  };

  confirmEditGroup = () => {
    const { group: name, group_id, group_index } = this.state;
    if(!group_id) {
      this.addGroup(name)
    } else {
      this.updateGroup(name, group_id, group_index)
    }
  };

  addGroup = name => {
    Modal.confirm({
      title: "确定添加一个分组吗？",
      onOk: () => {
        if(name) {
          axios.post("/gonghaoTag", {name})
            .then(res => {
              if(res.data.code === 1 && res.data.exist === 0) {
                const { data, msg } = res.data;
                this.setState(state => {
                  let gonghaoTagList = [...state.gonghaoTagList];
                  gonghaoTagList.push(data);
                  return {gonghaoTagList};
                });
                message.success(msg);
                this.closeEditGroupModal();
              } else if (res.data.code === 1 && res.data.exist === 1) {
                message.warning("分组已存在，请更换分组名称");
              } else {
                message.error(res.data.msg)
              }
            });
        } else {
          message.error("分组名称不能为空")
        }
      }
    });
  };

  updateGroup = (name, id, index) => {
    Modal.confirm({
      title: "确定修改分组名称吗？",
      onOk: () => {
        if(name) {
          axios.put("/gonghaoTag", {name, id})
            .then(res => {
              if(res.data.code === 1) {
                const { data, msg } = res.data;
                this.setState(state => {
                  let gonghaoTagList = state.gonghaoTagList;
                  Object.assign(gonghaoTagList[index], data);
                  return {gonghaoTagList};
                });
                message.success(msg);
                this.closeEditGroupModal();
              }else {
                message.error(res.data.msg)
              }
            });
        } else {
          message.error("分组名称不能为空")
        }
      }
    });
  };

  deleteGroup = (id, index) => {
    Modal.confirm({
      title: "确定删除这个分组吗？",
      onOk: () => {
        axios.delete('/gonghaoTag/' + id)
          .then(res => {
            const {code , msg} = res.data;
            if(code === 1) {
              message.success(msg);
              this.getGroupGonghao();
              this.setState(state => {
                let gonghaoTagList = state.gonghaoTagList;
                gonghaoTagList.splice(index, 1);
                return {gonghaoTagList}
              })
            } else {
              message.error(msg)
            }
          })
      }
    })
  };

  changeGroupValue = e => {
    let group = e.target.value;
    this.setState({group})
  };

  searchGroupGonghao = nick_name => {
    if(nick_name) {
      axios.get('/conf/find_one', {params: {nick_name}})
        .then(res => {
          const {data: gonghaoList, code, msg} = res.data;
          if(code === 1) {
            message.success(msg);
            this.setState({gonghaoList})
          } else {
            message.error("没有查询到相关数据，请检查输入是否有误")
          }
        })
    } else {
      message.error("公号名称不能为空")
    }

  };

  closeGonghaoModal = () => {
    this.setState({
      gonghaoModal: false,
      gonghao_id: "",
      gonghao_index: -1,
      selectGroup: ""
    })
  };

  submitGonghaoModal = () => {
    const { gonghao_id: id, gonghao_index, selectGroup: group, selectedRowKeys: ids } = this.state;
    if(id) {
      this.updateOne(id, gonghao_index, group)
    } else {
      this.updateMore(ids, group)
    }
  };

  openUpdateMoreModal = () => {
    const {selectedRowKeys} = this.state;
    if(selectedRowKeys.length === 0) {
      message.warning("请先选择公号")
    } else {
      this.setState({
        gonghaoModal: true
      })
    }
  };

  updateOne = (id, gonghao_index, group) => {
    Modal.confirm({
      title: "确定修改分组吗？",
      onOk: () => {
        axios.put('/conf', {id, group})
          .then(res => {
            const { code, msg} = res.data;
            if(code === 1) {
              message.success(msg);
              this.setState(state => {
                let gonghaoList = [...state.gonghaoList];
                gonghaoList.splice(gonghao_index, 1);
                return {gonghaoList}
              });
              this.closeGonghaoModal();
            } else {
              message.error(msg);
            }
          })
      }
    })
  };

  updateMore = (ids, group) => {
    Modal.confirm({
      title: "确定修改分组1吗？",
      onOk: () => {
        axios.put('/conf/multi_select', {ids, group})
          .then(res => {
            const { code, msg } = res.data;
            if(code === 1) {
              message.success(msg);
              this.getGroupGonghao();
              this.closeGonghaoModal();
              this.setState({selectedRowKeys: []})
            } else {
              message.error(msg);
            }
          })
      }
    })
  };

  changeSelectGroup = e => {
    let selectGroup = e.target.value;
    this.setState({selectGroup});
  };

  render() {
    const { gonghaoTagList, gonghaoModal, editGroupModalVisible, gonghaoList, group, selectGroup } = this.state;;
    const addGroupStyle = {
      width: "90%",
      margin: "15px auto 0",
      padding: "10px 0",
      borderRadius: 5,
      border: "1px dashed #999"
    };
    const rowSelection = {
      selectedRowKeys: this.state.selectedRowKeys,
      onChange: selectedRowKeys => {
        this.setState({selectedRowKeys})
      }
    };
    return (
    	<div>
        <div className="container">
          <Layout>
            <Sider theme="light">
              <List
                header={<div className="cursor userSelect" onClick={() => this.getGroupGonghao("未分组")}>未分组</div>}
                bordered
                dataSource={gonghaoTagList}
                style={{marginRight: 10}}
                renderItem={(item, index) => (
                  <List.Item className="userSelect">
                    <div style={{display: "flex", flexDirection: "row", justifyContent: "space-between", width: "100%", lineHeight: 1}}>
                      <p className="cursor" onClick={() => this.getGroupGonghao(item.name)}>{item.name}</p>
                      <div>
                        <Icon className="cursor" style={{marginRight: 10}} type="form" onClick={() => this.setState({editGroupModalVisible: true, group_id: item._id, group_index: index})} />
                        <Icon className="cursor" type="close-square" onClick={() => this.deleteGroup(item._id, index)} />
                      </div>
                    </div>
                  </List.Item>
                )}
              />
              <div className="tc cursor userSelect" style={addGroupStyle} onClick={this.openEditGroupModal}>+ 添加分组</div>
            </Sider>
            <Content>
              <Card style={{padding: 20}}>
                <Search placeholder="请输入公众号名称" style={{width: 200, marginBottom: 20, marginTop: -10}} onSearch={this.searchGroupGonghao} />
                <Button type="primary" style={{display: 'block', marginBottom: 20}} onClick={this.openUpdateMoreModal}>批量修改</Button>
                <Table rowSelection={rowSelection} bordered size="small" rowKey={record => record._id} columns={this.columns} dataSource={gonghaoList} />
              </Card>
            </Content>
          </Layout>
        </div>
        <Modal visible={gonghaoModal} onCancel={this.closeGonghaoModal} onOk={this.submitGonghaoModal}>
          <Radio.Group value={selectGroup} onChange={this.changeSelectGroup}>
            <Radio value="未分组">未分组</Radio>
            {gonghaoTagList.map(item => <Radio key={item._id} value={item.name}>{item.name}</Radio>)}
          </Radio.Group>
        </Modal>
        <Modal visible={editGroupModalVisible} title="新建分组名称" onCancel={this.closeEditGroupModal} onOk={this.confirmEditGroup}>
          分组名称：<Input onChange={this.changeGroupValue} value={group} style={{width: 300}} placeholder="请输入分组名称" />
        </Modal>
    	</div>
    )
  }
}

export default GonghaoGroup;