import React, {Component} from 'react';
import {Empty, Modal, message, Tabs} from "antd";
import {connect} from "react-redux";
import {actionCreators} from "./store";
import './index.scss';
import GroupMaterial from './material';
import TextMaterial from './textMaterial';
import SingleSelectByGroup from "../../../common/components/SelectGonghao/SingleSelectByGroup";
import Button from '../../../common/components/Button';
import History from './history';
import Loading from '../../../common/components/Loading';

const { TabPane } = Tabs;

class groupMessage extends Component {

  state = {
    code: null,
    historyModalVisible: false,
    spinning: true
  };

  timer = () => setTimeout(() => {
    this.setState({spinning: false})
  }, 1000);

  setCode = code => {
    clearTimeout(this.timer);
    this.setState({spinning: true});
    this.setState({code});
    this.timer();
  };

  componentDidMount() {
    message.warning("请选择公号进行操作")
  }

  render() {
    const {getMaterial, clearMaterial} = this.props;
    const {code, historyModalVisible, spinning} = this.state;
    const imageStyle = {
      height: "auto",
      margin: "0 auto"
    };
    return (
      <div>
        <div className="container">
          <SingleSelectByGroup setCode={this.setCode} content={
            code
              ? spinning
                ? <div>
                    <Loading spinning={spinning}/><Empty description="" image="https://s.weituibao.com/static/1544235586856/empty.png" imageStyle={imageStyle}/>
                  </div>
                : <div>
                <Tabs tabPosition="left">
                  <TabPane tab="图文" key="1">
                    <Button size="default" background="success" buttonContent="同步素材" onClick={() => getMaterial(code)}/>
                    <Button size="default" buttonContent="群发记录" onClick={() => this.setState({historyModalVisible: true})}/>
                    <Button size="default" mb={20} background="danger" onClick={() => clearMaterial(code)} buttonContent="清空列表"/>
                    <GroupMaterial code={code}/>
                  </TabPane>
                  <TabPane tab="文本" key="2">
                    <TextMaterial code={code} />
                  </TabPane>
                </Tabs>
                  </div>
              : <Empty description="" image="https://s.weituibao.com/static/1544235586856/empty.png" imageStyle={imageStyle}/>}/>
        </div>
        {code && <Modal title="发送记录" visible={historyModalVisible} width={1200} footer="" onCancel={() => this.setState({historyModalVisible: false})}><History code={code}/></Modal>}
      </div>
    )
  }

}

const mapDispatch = dispatch => ({
  getMaterial(code) {
    dispatch(actionCreators.getMaterial(code))
  },
  clearMaterial(code) {
    dispatch(actionCreators.clearMaterial(code));
  },
});

export default connect(null, mapDispatch)(groupMessage);