import { combineReducers } from 'redux';
import { reducer as groupReducer } from './group/store';
import { reducer as kefuReducer } from './kefu/store';
import { reducer as templateReducer } from './template/store';

const reducer = combineReducers({
  group: groupReducer,
  kefu: kefuReducer,
  template: templateReducer
});

export default reducer;