import React, {Component, Fragment} from 'react';
import {Form, Input, Radio, Button, Icon, Upload, Divider} from 'antd';
import GraphicMessage from '../../../../common/components/GraphicMessage';

class MenuReplyMessage extends Component {
  render() {
    const {messageInfo, changeMessageInfo, uploadImg, updateCurrentMessage, deleteCurrentMessage, addToList, updateIndex, updateMessageItem} = this.props;
    let MessageContent;
    if (messageInfo.replyType === 0) {
      MessageContent = <Form.Item className="inputtxt" label="文本消息">
        <Input.TextArea onChange={e => changeMessageInfo("content", e.target.value)} value={messageInfo.content} autosize={{minRows: 3, maxRows: 6}} placeholder='请输入回复文本消息'/>
      </Form.Item>
    } else {
      let UploadImg;
      if (messageInfo.picurl) {
        UploadImg = <img style={{width: "100px"}} src={messageInfo.picurl} alt="图片走丢啦~"/>
      } else {
        UploadImg = <Button><Icon type="upload"/> 上传图片 </Button>
      }
      MessageContent = <Fragment>
        <Form.Item label="图文标题">
          <Input onChange={e => changeMessageInfo("title", e.target.value)} value={messageInfo.title} placeholder='请输入图文标题'/>
        </Form.Item>
        <Form.Item label="图文链接">
          <Input onChange={e => changeMessageInfo("url", e.target.value)} value={messageInfo.url} placeholder='请输入图文链接'/>
        </Form.Item>
        <Form.Item label="图文描述">
          <Input.TextArea onChange={e => changeMessageInfo("description", e.target.value)} value={messageInfo.description} autosize={{minRows: 3, maxRows: 6}} placeholder='请输入图文描述'/>
        </Form.Item>
        <Form.Item label="上传图片">
          <Upload name="imageFile" action="/reply/upload" showUploadList={false} onChange={uploadImg}>
            {UploadImg}
          </Upload>
        </Form.Item>
        <Form.Item label=" " colon={false}>
          {updateIndex > -1 ? <div>
            <Button onClick={updateCurrentMessage} type="primary" style={{marginRight: 20}}>保存</Button>
            <Button onClick={deleteCurrentMessage}>删除此条消息</Button>
          </div> : <div><Button onClick={addToList}>添加到图文列表</Button><p className="tips">Tips: 当用户发送文本、图片、视频、图文、地理位置这五种消息时，只能回复1条图文消息；其余场景最多可回复8条图文消息</p></div>}
        </Form.Item>
        <Divider dashed />
        <div style={{width: "50%", margin: "auto"}}>{messageInfo.articles.length > 0 && <GraphicMessage messageList={messageInfo.articles} updateMessageItem={updateMessageItem} />}</div>
      </Fragment>
    }
    return (
    	<div>
        <Form.Item wrapperCol={{offset: 2}}>
          <Radio.Group value={messageInfo.replyType} onChange={e => changeMessageInfo("replyType", e.target.value)}>
            <Radio.Button value={0}>文本消息</Radio.Button>
            <Radio.Button value={1}>图文消息</Radio.Button>
          </Radio.Group>
        </Form.Item>
        {MessageContent}
    	</div>
    )
  }
}

export default MenuReplyMessage;