import React, { Component } from 'react';
import { Route, Redirect, Switch } from "react-router-dom";
import Group from "./group";
import GroupMaterial from "./group/material";
import History from './group/history';
import Kefu from './kefu';
import EditKefuMSg from './kefu/edit';
import Template from './template';
import EditTemplate from './template/edit';

class GroupRouter extends Component {
  render() {
    return (
    	<div>
        <Route path='/admin/message'>
          <Switch>
            <Route path="/admin/message/group/index" component={Group} />
            <Route path="/admin/message/group/material" component={GroupMaterial} />
            <Route path="/admin/message/group/history" component={History} />
            <Route path="/admin/message/kefu/index" component={Kefu} />
            <Route path="/admin/message/kefu/create" component={EditKefuMSg} />
            <Route path="/admin/message/kefu/update" component={EditKefuMSg} />
            <Route path="/admin/message/template/index" component={Template} />
            <Route path="/admin/message/template/create" component={EditTemplate} />
            <Route path="/admin/message/template/update" component={EditTemplate} />
            <Redirect from="/admin/message" exact to='/admin/message/group' />
            <Redirect from="/admin/message/group" exact to='/admin/message/group/index' />
            <Redirect from="/admin/message/kefu" exact to='/admin/message/kefu/index' />
            <Redirect from="/admin/message/template" exact to='/admin/message/template/index' />
          </Switch>
        </Route>
    	</div>
    )
  }
}

export default GroupRouter;