import React, {Component} from 'react';
import {connect} from 'react-redux';
import {
  ConfigProvider,
  Icon,
  Form,
  Button,
  Radio,
  Input,
  Avatar,
  Divider,
  Select,
  Row,
  Col,
  Modal,
  Switch,
  DatePicker
} from "antd";
import SingleSelectModal from '../../../../common/components/SelectGonghao/SingleSelectModal';
import {actionCreators} from "../store";
import zhCN from 'antd/es/locale/zh_CN';
import moment from "moment";
import 'moment/locale/zh-cn';
import TemplateModal from '../components/TemplateModal';
import './index.scss';

const {Option} = Select;

class EditTemplate extends Component {

  async componentWillMount() {
    this.props.getAllCodes();
    const {query} = this.props.location;
    query && await this.props.initMessageInfo(query);
  }

  render() {
    const formItemLayout = {
      labelCol: {
        sm: {span: 4}
      },
      wrapperCol: {
        sm: {span: 20, offset: 0}
      },
    };

    const {openSelectModal, messageInfo, changeMessageInfo, selectGonghaoModal, allCodes, openEditModal, templateList, templateInfo, templateModalVisible, changeTemplate, history, submitMessage, confirmSelect, closeSelectModal, openPreviewModal, closePreviewModal, openid, changeOpenId, previewModal, preview} = this.props;
    let template = [];
    for (let key in templateInfo.data) {
      let content = messageInfo.content[key];
      if (key === "first") {
        template.unshift(<Form.Item label={templateInfo.data["first"].pre || ""} key="first">
          <span className="mr5">{content.value}</span>
          <Icon className="cursor" onClick={() => openEditModal(key, content)} type="edit"/>
        </Form.Item>)
      } else {
        template.push(<Form.Item label={templateInfo.data[key].pre || ""} key={key}>
          <span className="mr5">{content.value}</span>
          <Icon className="cursor" onClick={() => openEditModal(key, content)} type="edit"/>
        </Form.Item>)
      }
    }
    let GonghaoModal;
    selectGonghaoModal && (GonghaoModal =
      <SingleSelectModal code={messageInfo.code} confirmSelect={confirmSelect} closeSelectModal={closeSelectModal}
                         allCodes={allCodes} selectGonghaoModal={selectGonghaoModal}/>);
    return (
      <ConfigProvider locale={zhCN}>
        <div className="container edit-message">
          <Form className="kefu-form" {...formItemLayout} style={{width: 520}}>
            <Form.Item label="选择公号">
              <Avatar onClick={openSelectModal} className="cursor" shape="square" icon="user"/>
            </Form.Item>
            <Form.Item label="消息名称">
              <Input value={messageInfo.name} onChange={e => changeMessageInfo("name", e.target.value)}
                     placeholder="请输入消息名称,(仅供备注，不会发送给粉丝)"/>
            </Form.Item>
            <Divider style={{width: "175%", marginLeft: -50}}/>
            <h2 className="block-title">设置模板消息</h2>
            <Form.Item label="选择模板">
              <Select value={messageInfo.templateId} onChange={changeTemplate}>
                {templateList.map(item => <Option value={item.template_id}
                                                  key={item.template_id}>{item.title}</Option>)}
              </Select>
            </Form.Item>
            <Row className="template-wrapper">
              <Col span={4}/>
              <Col span={20}>
                <div className="message-block">
                  {template}
                </div>
              </Col>
            </Row>
            <Form.Item label="点击跳转">
              <Input value={messageInfo.url} placeholder="输入跳转链接，且必须以http://或https://开头"
                     onChange={e => changeMessageInfo("url", e.target.value)}/>
            </Form.Item>
            <Divider style={{width: "175%", marginLeft: -50}}/>
            <h2 className="block-title">选择群发对象</h2>
            <Form.Item label="群发粉丝">
              <Radio.Group value={messageInfo.sex} onChange={e => changeMessageInfo("sex", e.target.value)}>
                <Radio value="all">全部粉丝</Radio>
                <Radio value="0">未知粉丝</Radio>
                <Radio value="1">男粉丝</Radio>
                <Radio value="2">女粉丝</Radio>
              </Radio.Group>
            </Form.Item>
            <Divider style={{width: "175%", marginLeft: -50}}/>
            <h2 className="block-title">设置发送方式</h2>
            <Form.Item label="定时发送">
              <Switch checked={messageInfo.is_timing} onChange={value => changeMessageInfo("is_timing", value)}/>
            </Form.Item>
            {messageInfo.is_timing && <Form.Item label="发送时间">
              <DatePicker showTime placeholder="选择时间" value={moment(messageInfo.timing_time)}
                          onChange={(value, dateString) => changeMessageInfo('timing_time', dateString)}/>
            </Form.Item>}
            <Divider style={{width: "175%", marginLeft: -50}}/>
            <Form.Item>
              <Button size="large" type="primary"
                      onClick={() => submitMessage(messageInfo, history, templateList)}>确定{!messageInfo._id ? "并创建消息" : "并保存消息"}</Button>
              {messageInfo._id &&
              <Button style={{marginLeft: 20}} type="info" size="large" onClick={() => history.goBack()}>取消编辑</Button>}
              <Button size="large" style={{marginLeft: 20, background: "yellowgreen", borderColor: "yellowgreen"}}
                      type="primary" onClick={openPreviewModal}>发送预览</Button>
            </Form.Item>
          </Form>
          {GonghaoModal}
          <TemplateModal show={templateModalVisible}/>
          <Modal title="发送预览" visible={previewModal} onCancel={closePreviewModal}
                 onOk={() => preview(messageInfo, openid)}>
            <Input onChange={changeOpenId} value={openid} placeholder="请输入预览用户的openid"/>
            <span className="tips">Tips: 在预览公号回复<span> openid </span>,将返回的内容复制到输入框中<br/>一个openid只对应一个公号</span>
          </Modal>
        </div>
      </ConfigProvider>
    )
  }

  componentWillUnmount() {
    this.props.resetState()
  }
}

const mapState = state => ({
  messageInfo: state.message.template.messageInfo,
  templateList: state.message.template.templateList,
  templateInfo: state.message.template.templateInfo,
  templateModalVisible: state.message.template.templateModalVisible,
  selectGonghaoModal: state.message.template.selectGonghaoModal,
  allCodes: state.message.template.allCodes,
  previewModal: state.message.template.previewModal,
  openid: state.message.template.openid,
});

const mapDispatch = dispatch => ({
  getAllCodes() {
    dispatch(actionCreators.getAllCodes())
  },
  openEditModal(key, content) {
    !content.color && (content.color = "#000");
    dispatch(actionCreators.openEditModal(key, content))
  },
  changeTemplate(value) {
    dispatch(actionCreators.changeTemplate(value))
  },
  submitMessage(info, history, templateList) {
    dispatch(actionCreators.submitMessage(info, history, templateList));
  },
  openSelectModal() {
    dispatch(actionCreators.openSelectModal())
  },
  changeMessageInfo(key, value) {
    dispatch(actionCreators.changeMessageInfo(key, value))
  },
  initMessageInfo(record) {
    dispatch(actionCreators.initMessageInfo(record))
  },
  confirmSelect(code) {
    dispatch(actionCreators.confirmSelect(code))
  },
  resetState() {
    dispatch(actionCreators.resetState())
  },
  closeSelectModal() {
    dispatch(actionCreators.closeSelectModal())
  },
  changeOpenId(e) {
    dispatch(actionCreators.changeOpenId(e.target.value))
  },
  openPreviewModal() {
    dispatch(actionCreators.openPreviewModal())
  },
  closePreviewModal() {
    dispatch(actionCreators.closePreviewModal())
  },
  preview(info, openid) {
    dispatch(actionCreators.preview(info, openid))
  },
});

export default connect(mapState, mapDispatch)(EditTemplate);