import React, { Component } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import Account from './index';
import AddAccount from './add';
class AccountRouter extends Component {
  render() {
    return (
    	<Switch>
        <Route path="/admin/account/list" component={Account} />
        <Route path="/admin/account/add" component={AddAccount} />
        <Redirect from="/admin/account" exact to="/admin/account/list"/>
    	</Switch>
    )
  }
}

export default AccountRouter;