import React from 'react';
import {Layout, Card} from 'antd';
import HeaderWrapper from './header';
import SiderWrapper from './sider';
import BreadCrumb from './bread';
import "./index.scss";

const {Content, Sider, Header, Footer} = Layout;

const BasicLayout = props => <Layout className="basic-layout">
  <HeaderWrapper/>
  <Layout>
    <Sider collapsible  theme="light" width="180" style={{minHeight: "calc(100vh - 50px)"}}>
      <SiderWrapper/>
    </Sider>
    <Layout>
      <Content style={{marginLeft: 2}}>
        <Card style={{minHeight: "calc(100vh - 90px)"}}>
          <Layout>
            <Header style={{ background: '#fff', padding: 0, borderBottom: "2px solid #eee", height: 43 }}><BreadCrumb /></Header>
            <Content>{props.content}</Content>
          </Layout>
        </Card>
      </Content>
      <Footer style={{ textAlign: 'center', padding: "10px 0" }}>Copyright&copy;2019  最终解释权归本公司所有</Footer>
    </Layout>
  </Layout>
</Layout>;

export default BasicLayout;