import * as constants from './constants';

const firstMenuForm = {
  name: "菜单名称",
  type: "view",
  key: "",
  url: "",
  sub_button: []
};

const sub_button = {
  name: "子菜单名称",
  type: "view",
  key: "",
  url: ""
};

const defaultMenuInfo = {
  title: "",
  individual: false,
  sex: "0",
  values: [],
  codes: [],
  contents: {}
};

const messageInfo = {
  replyType: 0,
  articles: [],
  content: ""
};

const defaultState = {
  menuList: [],
  menuForm: JSON.parse(JSON.stringify(firstMenuForm)),
  menuInfo: JSON.parse(JSON.stringify(defaultMenuInfo)),
  menuIndex: -1,
  sub_button: JSON.parse(JSON.stringify(sub_button)),
  secMenuIndex: -1,
  isSubmit: false,
  updateIndex: -1,
  messageInfo: JSON.parse(JSON.stringify(messageInfo)),
  currentKey: ""
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    // 菜单配置列表页
    case constants.GET_MENU_LIST:    // 获取菜单列表
      newState.menuList = action.menuList;
      return newState;
    case constants.DELETE_MENU:      // 删除菜单
      newState.menuList.splice(action.index, 1);
      return newState;

    // 编辑菜单栏
    case constants.GET_ALL_CODES:    // 获取所有公号
      newState.allCodes = [...action.allCodes];
      return newState;
    case constants.CHANGE_GONGHAO:   // 选择公号
      newState.menuInfo.codes = newState.targetKeys = [...action.targetKeys];
      return newState;
    case constants.ADD_FIRST_MENU:    // 添加一级菜单
      newState.menuForm = JSON.parse(JSON.stringify(firstMenuForm));
      newState.menuInfo.values.push({...newState.menuForm});
      newState.menuIndex = newState.menuInfo.values.length - 1;
      return newState;
    case constants.EDIT_MENU:         // 编辑一级菜单
      newState.secMenuIndex = -1;
      newState.menuIndex = action.index;
      newState.menuForm = {...action.data};
      newState.menuForm.sub_button.length > 0 && (newState.sub_button = {...newState.menuForm.sub_button[0]});
      newState.menuForm.sub_button.length > 0 && (newState.secMenuIndex = 0);
      newState.currentKey = "";
      if(newState.menuIndex > -1) {
        if(newState.sub_button.key && newState.secMenuIndex > -1){
          newState.menuInfo.values[newState.menuIndex].key = newState.menuForm.key = "";
          newState.currentKey = newState.sub_button.key;
          newState.messageInfo = newState.menuInfo.contents[newState.currentKey]
        } else if(action.data.key) {
          newState.currentKey = newState.menuForm.key = newState.menuInfo.values[newState.menuIndex].key;
          newState.messageInfo = newState.menuInfo.contents[newState.currentKey]
        }
      }
      return newState;
    case constants.DELETE_FIR_MENU:   // 删除一级菜单
      newState.menuInfo.values.splice(newState.menuIndex, 1);
      newState.menuIndex = 0;
      newState.menuForm = newState.menuInfo.values[0] || JSON.parse(JSON.stringify(firstMenuForm));
      delete newState.menuInfo.contents[newState.currentKey];
      newState.menuForm.key &&( newState.messageInfo = newState.menuInfo.contents[newState.menuForm.key]);
      return newState;
    case constants.HANDLE_INPUT_CHANGE:    // 一级菜单input修改
      if(action.name === "type") {
        newState.menuForm.url = "";
        newState.messageInfo = JSON.parse(JSON.stringify(messageInfo));
        if(action.value === "click") {
          // newState.menuForm.key = newState.currentKey = newState.menuInfo.values[newState.menuIndex].key = "wxb_mn_" + (newState.menuIndex + 1) + "_0";
          newState.menuForm.key = newState.menuInfo.values[newState.menuIndex].key  = newState.currentKey;
        } else {
          newState.menuForm.key = newState.menuInfo.values[newState.menuIndex].key = newState.currentKey = ""
        }
      }
      newState.menuForm[action.name] = action.value;
      newState.menuInfo.values.splice(newState.menuIndex, 1, {...newState.menuForm});
      return newState;
    case constants.ADD_SECOND_MENU:   // 添加二级菜单
      newState.sub_button = JSON.parse(JSON.stringify(sub_button));
      newState.menuForm.sub_button.push({...newState.sub_button});
      newState.messageInfo = JSON.parse(JSON.stringify(messageInfo));
      newState.menuInfo.values.splice(newState.menuIndex, 1, {...newState.menuForm});
      newState.secMenuIndex = newState.menuForm.sub_button.length - 1;
      return newState;
    case constants.EDIT_SEC_MENU:     // 编辑二级菜单
      newState.menuInfo.contents[newState.currentKey] = newState.messageInfo;
      newState.secMenuIndex = action.index;
      newState.sub_button = {...action.data};
      newState.currentKey = "";
      if(action.data.key && newState.secMenuIndex > -1) {
        newState.menuInfo.values[newState.menuIndex].key = newState.menuForm.key = "";
        newState.currentKey = newState.sub_button.key;
        newState.messageInfo = newState.menuInfo.contents[newState.currentKey]
      }
      return newState;
    case constants.DELETE_SUB_MENU:   // 删除二级菜单
      newState.menuForm.sub_button.splice(newState.secMenuIndex, 1);
      newState.menuInfo.values[newState.menuIndex].sub_button.splice(newState.secMenuIndex, 1);
      newState.secMenuIndex = 0;
      newState.sub_button = newState.menuForm.sub_button[0] ||JSON.parse(JSON.stringify(sub_button));
      delete newState.menuInfo.contents[newState.currentKey];
      newState.sub_button.key && (newState.messageInfo = newState.menuInfo.contents[newState.sub_button.key]);
      return newState;
    case constants.HANDLE_SUB_INPUT_CHANGE:    // 二级菜单input修改
      if(action.name === "type") {
        newState.sub_button.url = "";
        newState.messageInfo = JSON.parse(JSON.stringify(messageInfo));
        if(action.value === "click") {
          newState.menuInfo.values[newState.menuIndex].key = newState.menuForm.key = "";
          newState.sub_button.key = newState.currentKey;
        } else {
          newState.sub_button.key = newState.currentKey = ""
        }
      }
      newState.sub_button[action.name] = action.value;
      newState.menuForm.sub_button.splice(newState.secMenuIndex, 1, {...newState.sub_button});
      newState.menuInfo.values.splice(newState.menuIndex, 1, {...newState.menuForm})
      return newState;
    case constants.CHANGE_MESSAGE_INFO:  // 修改图文消息Input
      if(action.key === "replyType") {
        newState.messageInfo = JSON.parse(JSON.stringify(messageInfo));
        newState.messageInfo.replyType = action.value
      } else {
        newState.messageInfo[action.key] = action.value;
      }
      return newState;
    case constants.ADD_TO_LIST:          // 添加到图文消息列表
      const { title, picurl, url, description } = newState.messageInfo;
      newState.messageInfo.articles.push({...{ title, picurl, url, description }});
      newState.messageInfo.title = newState.messageInfo.picurl = newState.messageInfo.url = newState.messageInfo.description = "";
      return newState;
    case constants.UPDATE_MESSAGE_ITEM:  // 选择修改的消息
      const { item, index } = action;
      Object.assign(newState.messageInfo, {...item});
      newState.updateIndex = index;
      return newState;
    case constants.UPDATE_CURRENT_MESSAGE:    // 修改当前的消息
      let currentMessage = {
        title: newState.messageInfo.title,
        picurl: newState.messageInfo.picurl,
        url: newState.messageInfo.url,
        description: newState.messageInfo.description};
      newState.messageInfo.articles.splice(newState.updateIndex, 1, {...currentMessage});
      newState.messageInfo.title = newState.messageInfo.picurl = newState.messageInfo.url = newState.messageInfo.description = "";
      newState.updateIndex = -1;
      return newState;
    case constants.DELETE_CURRENT_MESSAGE:     // 删除当前的消息
      newState.messageInfo.articles.splice(newState.updateIndex, 1);
      newState.messageInfo.title = newState.messageInfo.picurl = newState.messageInfo.url = newState.messageInfo.description = "";
      newState.updateIndex = -1;
      return newState;
    case constants.CLEAR_MESSAGE_INFO_INPUT:   // 清空messageInfo的input
      newState.messageInfo.title = newState.messageInfo.picurl = newState.messageInfo.url = newState.messageInfo.description = "";
      newState.updateIndex = -1;
      return newState;
    case constants.SAVE_TO_CONTENTS:           // 保存到contents
      let {articles, content, replyType} = newState.messageInfo;
      let message = {articles, content, replyType};
      newState.currentKey && (newState.menuInfo.contents[newState.currentKey] = {...message});
      return newState;
    case constants.RANDOM_STRING:              // 随机8位字符串
      newState.currentKey = action.result;
      return newState;

    case constants.OPEN_MADAL:        // 打开选择公号Modal
      newState.isSubmit = true;
      return newState;
    case constants.SUBMIT_MENU:       // 提交菜单栏
      newState.isSubmit = false;
      return newState;
    case constants.CANCEL_MENU:       // 取消提交、关闭Modal
      newState.isSubmit = false;
      return newState;
    case constants.HANDLE_INFO_CHANGE:         // 修改menuInfo
      newState.menuInfo[action.name] = action.value;
      return newState;
    case constants.HANDLE_INDIVIDUAL_CHANGE:   // 是否开启个性化菜单
      newState.menuInfo.individual = action.value;
      Object.assign(newState.menuForm, {
        sex: 0
      });
      return newState;

    case constants.INIT_MENU_INFO:    // 修改菜单栏时，初始化菜单栏信息
      newState.menuInfo = action.record;
      newState.menuInfo.id = action.record._id;
      newState.targetKeys = [...newState.menuInfo.codes];
      newState.menuForm = {...newState.menuInfo.values[0]};
      newState.menuIndex = 0;
      if(newState.menuForm.sub_button.length > 0) {
        newState.sub_button = {...newState.menuForm.sub_button[0]};
        newState.secMenuIndex = 0;
        if(newState.sub_button.key) {
          newState.currentKey = newState.sub_button.key;
          newState.messageInfo = newState.menuInfo.contents[newState.currentKey];
        }
      } else {
        newState.menuForm.key && (newState.currentKey = newState.menuForm.key);
        newState.currentKey && (newState.messageInfo = newState.menuInfo.contents[newState.currentKey]);
      }
      return newState;

    case constants.RESET_STATE:       // 重置state
      newState = defaultState;
      return newState;
    default:
      return state;
  }
};