import React from 'react';
import {Card, List} from 'antd';
import "./index.scss";

const GraphicMessage = props => {
  const {messageList, updateMessageItem} = props;
  return (
    <div className="graphic-message-list-wrapper">
      <Card className="message-list-card" title={
        messageList.length > 1 ?
        <List className="message-list">
            <List.Item className="cursor" onClick={() => updateMessageItem(messageList[0], 0)}>
              <div className="head-message">
                <p className="ellipse-two">{messageList[0].title}</p>
                <img src={messageList[0].picurl} alt=""/>
              </div>
          </List.Item>
          {messageList.slice(1).map((message, index) => 
            <List.Item className="border_btm cursor" onClick={() => updateMessageItem(message, index + 1)} key={index}>
              <div className="rest-message">
                <p className="ellipse-two">{message.title}</p>
                <img src={message.picurl} alt=""/>
              </div>
            </List.Item>)}
        </List> : <div className="cursor" onClick={() => updateMessageItem(messageList[0], 0)}>
          <div className="head-other">
            <img src={messageList[0].picurl} alt=""/>
            <h3 className="title textEllipsis">{messageList[0].title}</h3>
            <p className="ellipse-two">{messageList[0].description}</p>
          </div>
        </div>}
      />
    </div>
  )
};

export default GraphicMessage;