import React from 'react';
import { Checkbox, Row, Col, Modal, Button } from 'antd';

const MultiSelectModal = props => {
  const {selectGonghaoModal, allCodes, codes = [], changeGonghao, closeSelectModal} = props;
  return (
    <Modal width={1000} visible={selectGonghaoModal} maskClosable={false} closable={false} mask={false}  footer={<Button type="primary" disabled={codes.length === 0} onClick={closeSelectModal}>确定</Button>}>
      <Checkbox.Group value={codes} style={{ width: '100%' }} onChange={changeGonghao}>
        <Row>
          {allCodes.map(item =>  <Col span={4} key={item._id}>
            <Checkbox value={item.code}>{item.nick_name}</Checkbox>
          </Col>)}
        </Row>
      </Checkbox.Group>
    </Modal>
  )
};

export default MultiSelectModal;