import axios from 'axios';
import * as constants from './constants';
import { Modal, message } from 'antd';


// 公号配置

export const getReplyMessageList = () => {
  return dispatch => {
    axios.get('/reply')
      .then(res => {
        let replyMessageList = res.data.data;
        const action = {
          type: constants.GET_REPLY_MESSAGE_LIST,
          replyMessageList
        };
        dispatch(action)
      })
  }
};

export const deleteReplyMessage = (_id, index) => {
  return (dispatch) => {
    Modal.confirm({
      okText: "确定",
      cancelText: "取消",
      content: "确认删除吗？",
      title: "提示",
      onOk: () => {
        axios.get('/reply/del', {params: {_id}})
          .then(res => {
          const action = {
            type: constants.DELETE_REPLY_MESSAGE,
            index
          };
          dispatch(action);
          message.success("删除成功")
        });
      }
    })

  }
};


export const getAllCodes = () => {
  return dispatch => {
    axios.get('/conf')
      .then(res => {
        if (res.data.data.length > 0) {
          const {data: allCodes} = res.data;
          dispatch({ type: constants.GET_ALL_CODES, allCodes });
        }
      })
  }
};

export const closeSelectModal = () => ({
  type: constants.CLOSE_SELECT_MODAL
});

export const openSelectModal = () => ({
  type: constants.OPEN_SELECT_MODAL
});

export const changeMessageInfo = (key, value) => ({
  type: constants.CHANGE_MESSAGE_INFO,
  key, value
});

export const addToList = () => ({
  type: constants.ADD_TO_LIST
});

export const updateMessageItem = (item, index) => ({
  type: constants.UPDATE_MESSAGE_ITEM,
  item, index
});

export const updateCurrentMessage = () => ({
  type: constants.UPDATE_CURRENT_MESSAGE
});

export const deleteCurrentMessage = () => ({
  type: constants.DELETE_CURRENT_MESSAGE
});

export const createConfigForm = (configInfo, history) => {
  return (dispatch) => {
    Modal.confirm({
      title: "确定新建一条规则吗？",
      onOk: () => {
        axios.post("/reply/create", configInfo).then(res => {
          history.goBack();
          const action = {
            type: constants.CREATE_CONFIG_FORM,
            configInfo
          };
          dispatch(action);
          message.success("创建成功")
        });
      }
    })
  }
};

export const updateConfigForm = (configInfo, history, index) => {
  return (dispatch) => {
    Modal.confirm({
      title: "确定修改吗？",
      onOk: () => {
        axios.post("/reply/update", configInfo).then(res => {
          history.goBack();
          const action = {
            type: constants.UPDATE_CONFIG_FORM,
            configInfo, index
          };
          dispatch(action);
          message.success("修改成功")
        });
      }
    })
  }
};

export const resetState = () => ({
  type: constants.RESET_STATE
});

export const initState = (record, index) => ({
  type: constants.INIT_STATE,
  record, index
});