import * as constants from './constants';

const defaultState = {
  messageList: [],
  isComplete: true,
  tagList: [],
  sendModal: false,
  sendTimingModal: false,
  date: "",
  time: "",
  sendInfo: {
    id: "",
    tagId: null,
    mediaId: ""
  },
  sendTimingInfo: {
    id: "",
    tagId: null,
    isTiming: false,
    timing: null
  },
  updateInfo: {
    "media_id": "",
    "index": null,
    "articles": {
      "content_source_url": ""
    },
    id: ""
  },
  updateMaterialModal: false,

  historyList: [],
  historyMsgModal: false,
  delMsgInfo: {
    code: null,
    msg_id: "",
    article_idx: ""
  },
  delDate: ""
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {

    // 群发素材

    case constants.GET_GROUP_MATERIAL:
      newState.messageList = [...action.data];
      return newState;
    case constants.GET_TAG_LIST:
      newState.tagList = [...action.data];
      return newState;
    case constants.CHANGE_TAG_ID:
      !action.isTiming && (newState.sendInfo.tagId = action.value)
      action.isTiming && (newState.sendTimingInfo.tagId = action.value)
      return newState;
    case constants.CLOSE_MODAL:
      newState.sendInfo = newState.sendTimingInfo = {};
      newState.sendTimingInfo.timing = Date.now();
      newState.sendTimingInfo.isTiming = newState.sendTimingModal = newState.sendModal = false;
      return newState;
    case constants.OPEN_MODAL:
      newState.sendInfo.id = action.record._id;
      newState.sendInfo.tagId = action.record.tagId;
      newState.sendInfo.mediaId = action.record.media_id;
      newState.sendModal = true;
      return newState;
    case constants.OPEN_TIMING_MODAL:
      newState.sendTimingInfo.id = action.record._id;
      newState.sendTimingInfo.tagId = action.record.tagId;
      newState.sendTimingInfo.isTiming = action.record.isTiming;
      newState.sendTimingInfo.timing = action.record.timing;
      newState.sendTimingModal = true;
      return newState;
    case constants.CHANGE_TIMING_STATE:
      newState.sendTimingInfo.isTiming = action.value;
      return newState;
    case constants.CHANGE_TIME:
      if (action.timeType === "date") {
        newState.date = action.time
      } else {
        newState.time = action.time
      }
      let dateTime = new Date(newState.date + " " + newState.time);
      newState.sendTimingInfo.timing = dateTime.getTime();
      return newState;
    case constants.COPY:
      newState.messageList.unshift({...action.data});
      return newState;
    case constants.DELETE_MATERIAL:
      newState.messageList.splice(action.index, 1);
      return newState;
    case constants.CLEAR_MATERIAL:
      newState.messageList = [];
      return newState;
    case constants.OPEN_UPDATE_MATERIAL_MODAL:
      newState.updateMaterialModal = true;
      newState.updateInfo = action.updateInfo;
      return newState;
    case constants.CLOSE_UPDATE_MATERIAL_MODAL:
      newState.updateMaterialModal = false;
      newState.updateInfo.articles.content_source_url = "";
      return newState;
    case constants.CHANGE_CONTENT_SOURCE_URL:
      newState.updateInfo.articles.content_source_url = action.content_source_url;
      return newState;

    // 群发历史
    case constants.GET_HISTORY_LIST:
      newState.historyList = [...action.data];
      return newState;
    case constants.OPEN_MSG_MODAL:
      newState.historyMsgModal = true;
      newState.delMsgInfo.code = action.code;
      newState.delMsgInfo.msg_id = action.msg_id;
      return newState;
    case constants.CLOSE_MSG_MODAL:
      newState.historyMsgModal = false;
      newState.delMsgInfo.article_idx = "";
      return newState;
    case constants.CHANGE_ARTICLE_IDX:
      newState.delMsgInfo.article_idx = action.value;
      return newState;
    case constants.CLEAR_HISTORY:
      newState.historyList = [];
      return newState;
    case constants.CHANGE_DATE:
      newState.delDate = action.delDate;
      return newState;

    case constants.RESET_STATE:
      newState = defaultState;
      return newState;
    default:
      return state;
  }
};