import React, {Component} from 'react';
import {Table, Modal, message, Tabs} from 'antd';
import axios from "axios";
import {Link} from 'react-router-dom';
import Button from '../../../common/components/Button';

const {TabPane} = Tabs;

class Advertise extends Component {
  constructor(props) {
    super(props);
    this.changeTab = this.changeTab.bind(this);
  }

  state = {
    spreadData: [],
    domain_names: [],
    tagList: [],
    dataList: []
  };

  render() {
    const {spreadData, tagList} = this.state;
    const tabsRender = tagList.map(item => (
      <TabPane tab={item.name} key={item.name}/>
    ));
    const spreadHeader = [
      {
        key: "links",
        title: "链接",
        align: "center",
        width: 280,
        render: (text, record) => <p
          style={{width: 280, wordWrap: "break-word", userSelect: "all"}}>{'http://tiexie0.wang/cmst/' + record.id}</p>
      },
      {
        key: "group",
        dataIndex: "group",
        title: "分组",
        align: "center",
        width: 120
      },
      {
        key: "title",
        dataIndex: "title",
        title: "标题",
        align: "center"
      },
      {
        key: "remarks",
        dataIndex: "remarks",
        title: "备注",
        align: "center",
        width: 100,
        render: text => text ? text : "无"
      },
      {
        title: "数据",
        align: "center",
        width: 80,
        render: (text, record) => <a href={'http://tiexie0.wang/transfer/' + record.id} target={"_blank"}>查看数据</a>
      },
      {
        title: "置顶",
        align: "center",
        width: 80,
        render: (text, record) => <Button background="warning" onClick={() => this.goTop(record._id)} buttonContent="置顶" />
      },
      {
        title: "Action",
        align: "center",
        width: 200,
        render: (text, record, index) => <div>
          <Button buttonContent={<Link to={{pathname: "./update", query: {...record}}}>修改</Link>} />
          <Button background="danger" buttonContent="删除" onClick={() => this.deleteLink(record._id, index)} />
          <Button background="success" mr={0} buttonContent="复制" onClick={() => this.copyLink({...record})} />
        </div>
      }
    ];
    return (
      <div>
        <div className="container">
          <Button size="default" background="success" mb={20} buttonContent={<Link to="./create">添加新链接</Link>} />
          <Tabs animated={false} defaultActiveKey="ALL" tabBarGutter={0} onChange={this.changeTab}
                size='small'>
            <TabPane tab="ALL" key="ALL"/>
            {tabsRender}
          </Tabs>
          <Table rowKey={record => record._id} size="small" bordered columns={spreadHeader} dataSource={spreadData}/>
        </div>
      </div>
    )
  }

  componentDidMount() {
    this.getSpreadList();
    this.getTagList();
  }

  getSpreadList() {
    axios.get("/transfer").then(res => {
      let spreadData = res.data.messages;
      let domain_names = res.data.domain_names;
      this.setState({
        spreadData,
        dataList: spreadData,
        domain_names
      })
    });
  }

  getTagList() {
    axios.get("/tuiguangTag").then(res => {
      const {data: tagList} = res.data;
      this.setState({tagList})
    });
  }

  changeTab(key) {
    const {dataList} = this.state;
    if (key === 'ALL') {
      this.setState({
        spreadData: dataList
      });
    } else {
      let data = [];
      for (let i = 0; i < dataList.length; i++) {
        if (key === dataList[i].group) {
          data.push(dataList[i]);
        }
      }
      this.setState({spreadData: [...data]})
    }
  }

  goTop(id) {
    Modal.confirm({
      title: "确定置顶这条链接吗？",
      onOk: () => {
        axios.post('/transfer/goTop', {id})
          .then(res => {
            message.info(res.data.success);
            this.getSpreadList();
          });
      }
    })
  }

  deleteLink(id, index) {
    const {spreadData} = this.state;
    Modal.confirm({
      title: '确认删除这条内容吗？:',
      onOk: () => {
        axios.get('/transfer/delete', {params: {id}})
          .then(res => {
            message.info(res.data.success);
            spreadData.splice(index, 1);
            this.setState({spreadData});
          });
      }
    })
  }

  copyLink(record) {
    record.id += '_copy';
    const {spreadData} = this.state;
    Modal.confirm({
      title: "确定复制一条推广链接吗？",
      onOk: () => {
        axios.post('/transfer/create', {...record})
          .then(res => {
            if (res.data.success) {
              message.info('复制成功');
              spreadData.unshift(res.data.data);
              this.setState({spreadData})
            } else {
              message.info(res.data.err);
            }
          });
      }
    })
  }
}

export default Advertise;