import * as constants from './constants';

const defaultState = {
  templateMessageList: [],
  confName: "",

  selectGonghaoModal: true,
  messageInfo: {
    name: "",
    code: null,
    templateId: "",
    templateName: "",
    url: "",
    sex: "all",
    content: {},
    is_timing: false,
    timing_time: new Date()
  },
  allCodes: [],
  templateList: [],
  templateInfo: {
    template_id: "",
  },
  name: "",
  templateModalVisible: false,
  content: {
    value: "",
    color: "#000"
  },
  previewModal: false,
  openid: ""
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {

    case constants.GET_TEMPLATE_LIST:
      newState.templateMessageList = action.data;
      newState.confName = "";
      return newState;
    case constants.DELETE_TEMPLATE:
      newState.templateMessageList.splice(action.index, 1);
      return newState;
    case constants.CHANGE_CONF_NAME:
      newState.confName = action.value;
      return newState;
    case constants.SEARCH_CONF_NAME:
      newState.templateMessageList = action.data;
      newState.confName = "";
      return newState;


    case constants.GET_ALL_CODES:
      newState.allCodes = action.allCodes;
      return newState;

    case constants.OPEN_SELECT_MODAL:
      newState.selectGonghaoModal = true;
      return newState;
    case constants.OPEN_EDIT_MODAL:
      newState.name = action.key;
      newState.content = action.content;
      newState.templateModalVisible = true;
      return newState;
    case constants.CLOSE_TEMPLATE_MODAL:
      newState.name = "";
      newState.templateModalVisible = false;
      newState.content = { value: "", color: "#000" };
      return newState;
    case constants.CHANGE_TEMPLATE:
      newState.templateInfo = newState.templateList.filter(item => item.template_id === action.value)[0];
      newState.messageInfo.templateId = newState.templateInfo.template_id;
      newState.messageInfo.content = {};
      for(let key in newState.templateInfo.data) {
        newState.messageInfo.content[key] = {
          value: "",
          color: "#000"
        }
      }
      return newState;
    case constants.EDIT_TEMPLATE:
      newState.content[action.key] = action.value;
      return newState;
    case constants.SAVE_TEMPLATE_MODAL:
      newState.messageInfo.content[newState.name] = newState.content;
      newState.name = "";
      newState.templateModalVisible = false;
      newState.content = { value: "", color: "#000" };
      return newState;

    case constants.CHANGE_MESSAGE_INFO:
      newState.messageInfo[action.key] = action.value;
      if(action.key === "is_timing") {
        newState.messageInfo.timing_time = new Date()
      }
      return newState;
    case constants.CONFIRM_SELECT:
      newState.templateList = action.templateList;
      newState.templateInfo = action.templateInfo;
      newState.messageInfo.templateId = newState.templateInfo.template_id;
      for(let key in newState.templateInfo.data) {
        newState.messageInfo.content[key] = {
          value: "",
          color: "#000"
        }
      }
      return newState;
    case constants.CLOSE_SELECT_MODAL:
      newState.selectGonghaoModal = false;
      return newState;
    case constants.OPEN_PREVIEW_MODAL:
      newState.previewModal = true;
      return newState;
    case constants.CLOSE_PREVIEW_MODAL:
      newState.previewModal = false;
      newState.openid = "";
      return newState;
    case constants.CHANGE_OPENID:
      newState.openid = action.value;
      return newState;

    case constants.INIT_MESSAGE_INFO:
      newState.messageInfo = JSON.parse(JSON.stringify(action.record));
      newState.templateList = action.templateList;
      newState.templateInfo = newState.templateList.filter(item => item.template_id === action.record.templateId)[0];
      return newState;

    case constants.RESET_STATE:
      newState = defaultState;
      return newState;
    default:
      return state;
  }
};