import React, {Component} from 'react';
import {Menu, Icon} from 'antd';
import {Link} from 'react-router-dom';

import './index.scss';

const {SubMenu} = Menu;
const MenuItem = Menu.Item;

class SideWrapper extends Component {
  menuList = [
    {
      keyIndex: 'gonghao',
      title: '公号管理',
      icon: 'appstore',
      role: 2,
      subMenuList: [
        {
          keyIndex: 'tuoguan',
          title: '公号托管',
          path: '/admin/gonghao/tuoguan'
        }, {
          keyIndex: 'menu',
          title: '菜单配置',
          path: '/admin/gonghao/menu'
        }, {
          keyIndex: 'reply',
          title: '自动回复',
          path: '/admin/gonghao/reply'
        // }, {
        //   keyIndex: 'qrcode',
        //   title: '参数二维码',
        //   path: '/admin/gonghao/qrcode'
        }
      ]
    }, {
      keyIndex: 'message',
      title: '消息管理',
      icon: 'snippets',
      role: 2,
      subMenuList: [
        {
          keyIndex: 'kefu',
          title: '客服消息',
          path: '/admin/message/kefu'
        }, {
          keyIndex: 'group',
          title: '群发消息',
          path: '/admin/message/group'
        }, {
          keyIndex: 'template',
          title: '模板消息',
          path: '/admin/message/template'
        }
      ]
    }, {
      keyIndex: 'channel',
      title: '渠道推广',
      icon: 'control',
      role: 2,
      subMenuList: [
        {
          keyIndex: 'spread',
          title: '推广链接',
          path: '/admin/channel/spread'
        }
      ]
    }, {
      keyIndex: 'account',
      title: '账户管理',
      icon: 'user',
      role: 0,
      subMenuList: [
        {
          keyIndex: 'list',
          title: '账户管理',
          path: '/admin/account/list'
        }, {
          keyIndex: 'add',
          title: '新增用户',
          path: '/admin/account/add'
        }
      ]
    }
  ];

  state = {
    selectedKeys: [],
    role: null
  };

  onSelect = async (selectItem) => {
    const latestSelectedKey = selectItem.selectedKeys.find(key => this.state.selectedKeys.indexOf(key) === -1);
    let selectList = [];
    await this.menuList.map(item => (selectList = [...selectList, ...item.subMenuList]));
    await selectList.find(item => (
      item.keyIndex === latestSelectedKey
      && this.setState({ selectedKeys: latestSelectedKey ? [latestSelectedKey] : [this.state.role === 2 ? 'tuoguan' : 'list'] })
    ))
  };

  UNSAFE_componentWillMount() {
    let userInfo = localStorage.getItem("userInfo");
    if(userInfo) {
      userInfo = JSON.parse(userInfo);
      this.setState({
        role: userInfo.role,
        selectedKeys: [window.location.pathname.split('/')[3] || (userInfo.role === 2 ? 'tuoguan' : 'list')]
      })
    }
  }

  render() {
    const pathName = window.location.pathname;
    const MenuListRender = this.menuList.map((item, index) => {
      if(item.role === this.state.role) {
        const SubMenuList = item.subMenuList.map(subItem => (
          <MenuItem key={subItem.keyIndex}><Link to={subItem.path}>{subItem.title}</Link></MenuItem>
        ));
        return (<SubMenu
          key={item.keyIndex}
          title={
            <span className='side-item'>
            <Icon className="side-icon" type={item.icon}/>
            <span>{item.title}</span>
          </span>
          }
        >
          {SubMenuList}
        </SubMenu>)
      }
      return '';
    });
    return (
      <div className='side-wrapper'>
        <Menu
          mode="inline"
          theme="light"
          selectedKeys={this.state.selectedKeys}
          defaultOpenKeys={[window.location.pathname.split('/')[2] || (this.state.role === 2 ? 'gonghao' : 'account')]}
          onSelect={this.onSelect}
          defaultSelectedKeys={[pathName]}
        >
          {MenuListRender}
        </Menu>
      </div>
    )
  }
}

export default SideWrapper;

