import axios from 'axios';
import * as constants from './constants';
import { Modal, message } from 'antd';

// 列表页

export const getTemplateList = () => {
  return dispatch => {
    axios.get("/template")
      .then(res => {
        const {data} = res.data;
        dispatch({ type: constants.GET_TEMPLATE_LIST, data })
      })
  }
};

export const deleteTemplate = (id, index) => {
  return dispatch => {
    Modal.confirm({
      title: "确定删除这条模板消息吗？",
      onOk: () => {
        axios.get("/template/del", {params: {id}})
          .then(res => {
            if(res.data.success) {
              message.success(res.data.success);
              dispatch({ type: constants.DELETE_TEMPLATE, index })
            }
          })
      }
    })
  }
};

export const sendTemplate = record => {
  return dispatch => {
    Modal.confirm({
      title: "确定发送这条模板消息吗？",
      onOk: () => {
        axios.post("/template/send", {...record})
          .then(res => {
            message.success(res.data);
          })
      }
    })
  }
};

export const changeConfNickName = value => ({
  type: constants.CHANGE_CONF_NAME,
  value
});

export const searchConfName = confName => {
  return dispatch => {
    axios.get('/template/search', {params: {confName: encodeURIComponent(confName)}})
      .then(res => {
        if(res.data.data.length > 0) {
          dispatch({type: constants.SEARCH_CONF_NAME, data: res.data.data});
          message.success(res.data.msg)
        } else {
          dispatch(getTemplateList());
          message.error("没有查询到相关数据")
        }
      })
  }
};


// 编辑模板消息
export const getAllCodes = () => {
  return dispatch => {
    axios.get('/conf')
      .then(res => {
        const action = {
          type: constants.GET_ALL_CODES,
          allCodes: res.data.data
        };
        dispatch(action);
      })
  }
};

export const openSelectModal = () => ({
  type: constants.OPEN_SELECT_MODAL
});

export const openEditModal = (key, content) => ({
  type: constants.OPEN_EDIT_MODAL,
  key, content
});

export const closeTemplateModal = () => ({
  type: constants.CLOSE_TEMPLATE_MODAL
});

export const changeTemplate = value => ({
  type: constants.CHANGE_TEMPLATE,
  value
});

export const saveTemplateModal = () => ({
  type: constants.SAVE_TEMPLATE_MODAL
});


export const editTemplate = (key, value) => ({
  type: constants.EDIT_TEMPLATE,
  key, value
});

export const changeMessageInfo = (key, value) => ({
  type: constants.CHANGE_MESSAGE_INFO,
  key, value
});

export const confirmSelect = code => {
  return dispatch => {
    dispatch(changeMessageInfo("code", code));
    axios.get('/template/list', {params: {code}})
      .then(res => {
        const action = {
          type: constants.CONFIRM_SELECT,
          templateList: res.data, templateInfo: {...res.data[0]}, code
        };
        dispatch(action);
      })
  }
};

export const closeSelectModal = () => ({
  type: constants.CLOSE_SELECT_MODAL
});


export const openPreviewModal = () => ({
  type: constants.OPEN_PREVIEW_MODAL
});

export const closePreviewModal = () => ({
  type: constants.CLOSE_PREVIEW_MODAL
});

export const preview = ({code, templateId, url, content}, openid) => {
  return dispatch => {
    axios.post('/template/preview', {code, templateId, url, content, openid})
      .then(res => {
        message.success(res.data.msg);
        dispatch(closePreviewModal());
      })
  }
};

export const changeOpenId = value => ({
  type: constants.CHANGE_OPENID,
  value
});

export const submitMessage = (info, history, templateList) => {
  return dispatch => {
    let url = '/template/create';
    info.templateName = templateList.filter(item => item.template_id === info.templateId)[0].title;
    info._id && (url = '/template/update') && (info.id = info._id);
    info.is_timing && (info.timing_time = new Date(info.timing_time));
    Modal.confirm({
      title: "确定" + (info._id ? "保存这" : "创建一") + "条模板消息吗？",
      onOk: () => {
        axios.post(url, {...info})
          .then(res => {
            if(res.data.success) {
              message.success(res.data.success);
              history.goBack()
            } else {
              message.error(res.data.err)
            }
          })
      }
    })
  }
};


export const initMessageInfo = record => {
  return dispatch => {
    dispatch(closeSelectModal());
    axios.get('/template/list', {params: {code: record.code}})
      .then(res => {
        const action = {
          type: constants.INIT_MESSAGE_INFO,
          templateList: res.data,
          record
        };
        dispatch(action);
      })
  }
};
