import React from 'react';
import { Radio, Row, Col, Modal, Button } from 'antd';

const SingleSelectModal = props => {
  const {selectGonghaoModal, allCodes, code, confirmSelect, closeSelectModal} = props;
  return (
    <Modal width={1000} visible={selectGonghaoModal} maskClosable={false} closable={false} mask={false}  footer={<Button type="primary" disabled={!code} onClick={closeSelectModal}>确定</Button>}>
      <Radio.Group value={code} style={{ width: '100%' }} onChange={e => confirmSelect(e.target.value, code)}>
        <Row>
          {allCodes.map(item => <Col span={4} key={item._id}>
            <Radio value={item.code}>{item.nick_name}</Radio>
          </Col>)}
        </Row>
      </Radio.Group>
    </Modal>
  )
};

export default SingleSelectModal;