import React, {Component} from 'react';
import "./index.scss";
import {Input, Icon, message} from 'antd';
import axios from '../../utils/http';


class Login extends Component {
  constructor(props) {
    super(props);
    this.login = this.login.bind(this);
    this.state = {
      loginInfo: {
        username: "",
        password: ""
      }
    }
  }

  render() {
    const { loginInfo } = this.state;
    const bgStyle = {
      minHeight: "100vh",
      backgroundSize: "100%",
      backgroundRepeat: "no-repeat",
      backgroundAttachment: "fixed",
      backgroundImage: `url(${require("../../assets/images/login_bg.png")})`
    };
    return (
      <div className="login-bg" style={bgStyle}>
        <div className="bg-mask"></div>
        <div className="content-wrapper">
          <div className="content">
            <p className="title">明星说后台管理系统</p>
            <p>运营与管控一体化的整合式服务平台</p>
            <p>一起解构数字世界，碰撞科技创新思想</p>
          </div>
        </div>
        <div className="login-form-wrapper">
          <div className="login-form">
            <h4 className="login-title">登录</h4>
            <Input
              value={loginInfo.username}
              size="large"
              onChange={e => this.changeInputValue(e.target.value, "username")}
              prefix={<Icon type="user" style={{color: 'rgba(0,0,0,.25)', marginRight: 10}}/>}
              style={{marginBottom: 30}}
              placeholder="用户名"
              onKeyUp={e => e.keyCode === 13 && this.login()}
            />
            <Input
              value={loginInfo.password}
              size="large"
              type="password"
              onChange={e => this.changeInputValue(e.target.value, "password")}
              prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)', marginRight: 10}}/>}
              placeholder="密码"
              onKeyUp={e => e.keyCode === 13 && this.login()}
            />
            <button className="login-btn cursor" onClick={this.login}>登录</button>
          </div>
        </div>
      </div>
    )
  }

  changeInputValue(value, name) {
    const { loginInfo } = this.state;
    loginInfo[name] = value;
    this.setState(state => ({loginInfo: {...state.loginInfo, ...loginInfo}}))
  }

  login() {
    const { loginInfo } = this.state;
    if(loginInfo.username && loginInfo.password) {
      loginInfo.password = this.$MD5(loginInfo.password).toString();
      axios.post('/account/login', {...loginInfo})
        .then(res => {
          const { code, msg, data } = res.data;
          if(code === -1) {
            message.error(msg);
          } else {
            const userInfo = JSON.stringify(data[0]);
            localStorage.setItem("userInfo", userInfo);
            window.location.href = '/admin';
            this.setState({loginInfo: {}});
            message.success(msg)
          }
        })
    } else {
      message.warning("用户名或密码不能为空");
    }
  }
}

export default Login;