import React, { Component } from 'react';
import { Button } from 'antd';

const ButtonList = {
  "warning": "#F39C12",
  "danger": "#E74C3C",
  "success": "#2ECC71",
  "info": "#7F8C8D",
  "update": "#3498DB"
};


class ButtonWrapper extends Component {
  state = {
    buttonStyle: {
      background: "#3498DB",
      color: "#fff",
      marginRight: 5,
      marginLeft: 0,
      marginBottom: 0
    }
  };
  componentDidMount() {
    const { background = "update", color = "#fff", mr = 5, ml = 0, mb = 0 } = this.props;
    this.setState({
      buttonStyle: { background: ButtonList[background], color, marginRight: mr, marginLeft: ml, marginBottom: mb }
    })
  }

  render() {
    const { size = "small", buttonContent, disabled = false, onClick } = this.props;
    const { buttonStyle } = this.state;
    return (
    	<Button size={size} disabled={disabled} style={buttonStyle} onClick={onClick}>{buttonContent}</Button>
    )
  }
}

export default ButtonWrapper;