import React, { Component } from 'react';
import { Form, Input, message, Modal, Select } from 'antd';
import Button from '../../../common/components/Button';
import axios from 'axios';

const { Option  } = Select;

class AddAccount extends Component {

  addAccount = () => {
    const { form: {validateFields} } = this.props;
    validateFields((errors, values) => {
      if(errors) {
        message.error("请检查输入是否有误");
        return
      }
      values.password = this.$MD5(values.password).toString();
      Modal.confirm({
        title: "确定创建一个新账户吗？",
        onOk: () => {
          axios.post('/account', {...values})
            .then(res => {
              if(res.data.code === 1) {
                this.resetForm()
                message.success(res.data.msg)
              } else {
                message.error(res.data.msg)
              }
            })
        }
      })
    })
    // console.log(getFieldsValue())
  };

  resetForm = () => {
    const { form: {setFieldsValue} } = this.props;
    setFieldsValue({username: "", password: "", role: 2})
  };

  render() {
    const { form: {getFieldDecorator} } = this.props;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 2 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 8 },
      },
      colon: false
    };
    return (

    	<div>
        <div className="container">
          <Form {...formItemLayout}>
            <Form.Item label="用户名">
              {getFieldDecorator('username', {
                rules: [{ required: true, message: '用户名不能为空' }],
              })(
                <Input placeholder="请输入用户名"/>
              )}
            </Form.Item>
            <Form.Item label="密码">
              {getFieldDecorator('password', {
                rules: [{ required: true, message: '密码不能为空' }],
              })(
                <Input placeholder="请输入密码"/>
              )}
            </Form.Item>
            <Form.Item label="备注">
              {getFieldDecorator('remarks', {
                rules: [{ required: true, message: '备注不能为空' }],
              })(
                <Input placeholder="请输入备注"/>
              )}
            </Form.Item>
            <Form.Item label="管理员权限">
              {getFieldDecorator('role', {
                rules: [{ required: true }],
                initialValue: 2
              })(
                <Select>
                  <Option value={0}>超级管理员</Option>
                  <Option value={1}>一级代理</Option>
                  <Option value={2}>二级代理</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item label=" ">
              <Button size="default" buttonContent="新增" onClick={this.addAccount} />
              <Button ml={20} background="info" size="default" buttonContent="清空" onClick={this.resetForm}  />
            </Form.Item>
          </Form>
        </div>
    	</div>
    )
  }
}

export default Form.create()(AddAccount);