import React, {Component, Fragment} from 'react';
import {connect} from "react-redux";
import {Form, Button, Input, Upload, Radio, Icon, Row, Col, Divider, Avatar} from "antd";
import * as actionCreators from '../store/actionCreators';
import "./index.scss";
import GraphicMessage from '../../../../common/components/GraphicMessage';
import MultiSelectModal from '../../../../common/components/SelectGonghao/MultiSelectModal';

const formItemLayout = {
  labelCol: {
    sm: {span: 4}
  },
  wrapperCol: {
    sm: {span: 14, offset: 0}
  },
};

class EditConfigInfo extends Component {

  UNSAFE_componentWillMount() {
    const { location: {state}, getAllCodes, initState, closeSelectModal } = this.props;
    getAllCodes();
    state && initState(state.record, state.index);
    state && closeSelectModal();
  }
  render() {
    const {selectGonghaoModal, allCodes, closeSelectModal, openSelectModal, submitForm, messageInfo, updateMessageItem, addToList, changeMessageInfo, uploadImg, history, updateIndex, updateCurrentMessage, deleteCurrentMessage} = this.props;
    let TextOrKey, MessageContent;
    if (messageInfo.type === 0) {
      TextOrKey = <Form.Item label="用户回复内容">
        <Input onChange={e => changeMessageInfo("text", e.target.value)} value={messageInfo.text} placeholder='请输入用户回复内容'/>
      </Form.Item>
    } else if (messageInfo.type === 1) {
      TextOrKey = <Form.Item label="点击关键词">
        <Input onChange={e => changeMessageInfo("key", e.target.value)} value={messageInfo.key} placeholder='请输入菜单栏点击关键词'/>
      </Form.Item>
    }
    if (messageInfo.replyType === 0) {
      MessageContent = <Form.Item className="inputtxt" label="文本消息">
        <Input.TextArea onChange={e => changeMessageInfo("content", e.target.value)} value={messageInfo.content} autosize={{minRows: 3, maxRows: 6}} placeholder='请输入回复文本消息'/>
      </Form.Item>
    } else {
      let UploadImg;
      if (messageInfo.picurl) {
        UploadImg = <img style={{width: "100px"}} src={messageInfo.picurl} alt="图片走丢啦~"/>
      } else {
        UploadImg = <Button><Icon type="upload"/> 上传图片 </Button>
      }
      MessageContent = <Fragment>
        <Form.Item label="图文标题">
          <Input onChange={e => changeMessageInfo("title", e.target.value)} value={messageInfo.title} placeholder='请输入图文标题'/>
        </Form.Item>
        <Form.Item label="图文链接">
          <Input onChange={e => changeMessageInfo("url", e.target.value)} value={messageInfo.url} placeholder='请输入图文链接'/>
        </Form.Item>
        <Form.Item label="图文描述">
          <Input.TextArea onChange={e => changeMessageInfo("description", e.target.value)} value={messageInfo.description} autosize={{minRows: 3, maxRows: 6}} placeholder='请输入图文描述'/>
        </Form.Item>
        <Form.Item label="上传图片">
          <Upload name="imageFile" action="/reply/upload" showUploadList={false} onChange={uploadImg}>
            {UploadImg}
          </Upload>
        </Form.Item>
        <Form.Item label=" " colon={false}>
          {updateIndex > -1 ? <div>
            <Button onClick={updateCurrentMessage} type="primary" style={{marginRight: 20}}>保存</Button>
            <Button onClick={deleteCurrentMessage}>删除此条消息</Button>
          </div> : <div><Button onClick={addToList}>添加到图文列表</Button><p className="tips">Tips: 当用户发送文本、图片、视频、图文、地理位置这五种消息时，只能回复1条图文消息；其余场景最多可回复8条图文消息</p></div>}
        </Form.Item>
      </Fragment>
    }
    return (
      <div className="container">
        {selectGonghaoModal && <MultiSelectModal allCodes={allCodes} selectGonghaoModal={selectGonghaoModal} changeGonghao={codes => changeMessageInfo("codes", codes)} closeSelectModal={closeSelectModal} codes={messageInfo.codes} />}
        <Form className="reply-form" labelAlign="right" {...formItemLayout}>
          <Col span={17}>
            <Form.Item label="选择公号">
              <Avatar className="cursor" shape="square" icon="user" onClick={openSelectModal}/>
            </Form.Item>
            <Form.Item label="消息名称">
              <Input onChange={e => changeMessageInfo("name", e.target.value)} value={messageInfo.name} placeholder='请输入消息名称,(仅供备注，不会发送给粉丝)'/>
            </Form.Item>
          </Col>
          <Divider style={{width: "110%", marginLeft: -50}}/>
          <h2 className="block-title">设置自动回复</h2>
          <Row>
            <Col span={17}>
              <Form.Item wrapperCol={{offset: 1}}>
                <Radio.Group value={messageInfo.replyType} onChange={e => changeMessageInfo("replyType", e.target.value)}>
                  <Radio.Button value={0}>文本消息</Radio.Button>
                  <Radio.Button value={1}>图文消息</Radio.Button>
                </Radio.Group>
              </Form.Item>
              {MessageContent}
            </Col>
            <Col span={7}>
              {messageInfo.articles.length > 0 && <GraphicMessage messageList={messageInfo.articles} updateMessageItem={updateMessageItem} />}
            </Col>
          </Row>
          <Divider style={{width: "110%", marginLeft: -50}}/>
          <h2 className="block-title">设置模板消息</h2>
          <Col span={17}>
            <Form.Item wrapperCol={{offset: 1}}>
              <Radio.Group value={messageInfo.type} onChange={e => changeMessageInfo("type", e.target.value)}>
                <Radio.Button value={0}>特定回复</Radio.Button>
                <Radio.Button value={2}>用户关注</Radio.Button>
                <Radio.Button value={4}>默认回复</Radio.Button>
              </Radio.Group>
            </Form.Item>
            <Form.Item label="粉丝性别">
              <Radio.Group value={messageInfo.sex} onChange={e => changeMessageInfo("sex", e.target.value)}>
                <Radio value={3}>默认</Radio>
                <Radio value={1}>男</Radio>
                <Radio value={2}>女</Radio>
                <Radio value={0}>未知</Radio>
              </Radio.Group>
            </Form.Item>
            <Form.Item label="未知性别">
              <Radio.Group value={messageInfo.attribute} onChange={e => changeMessageInfo("attribute", e.target.value)}>
                <Radio value={1}>男</Radio>
                <Radio value={2}>女</Radio>
              </Radio.Group>
            </Form.Item>
            {TextOrKey}
          </Col>
          <Divider style={{width: "110%", marginLeft: -50}}/>
          <Form.Item wrapperCol={{offset: 1}}>
            <Button type="primary" style={{marginRight: 20}} onClick={() => submitForm(messageInfo, this.props)}>提交</Button>
            <Button onClick={() => history.goBack()}>取消</Button>
          </Form.Item>
        </Form>
      </div>
    )
  }



  componentWillUnmount() {
    this.props.resetState()
  }
}

const mapState = state => ({
  messageInfo: state.gonghao.reply.messageInfo,
  allCodes: state.gonghao.reply.allCodes,
  selectGonghaoModal: state.gonghao.reply.selectGonghaoModal,
  updateIndex: state.gonghao.reply.updateIndex,
});

const mapDispatch = dispatch => ({
  getAllCodes() {
    dispatch(actionCreators.getAllCodes())
  },
  closeSelectModal() {
    dispatch(actionCreators.closeSelectModal())
  },
  openSelectModal() {
    dispatch(actionCreators.openSelectModal())
  },
  changeMessageInfo(key, value) {
    dispatch(actionCreators.changeMessageInfo(key, value))
  },
  uploadImg(info) {
    if (info.file.status === 'done') {
      let picurl = `http://t.dmmup.com/uploads/${info.file.response.filename}`;
      dispatch(actionCreators.changeMessageInfo("picurl", picurl))
    }
  },
  addToList() {
    dispatch(actionCreators.addToList())
  },
  updateMessageItem(item, index) {
    dispatch(actionCreators.updateMessageItem(item, index))
  },
  updateCurrentMessage() {
    dispatch(actionCreators.updateCurrentMessage())
  },
  deleteCurrentMessage() {
    dispatch(actionCreators.deleteCurrentMessage())
  },
  submitForm(messageInfo, props) {
    const { title, picurl, url, description } = messageInfo;
    const {state} = props.location;
    messageInfo.articles.length === 0 && (messageInfo.articles.push({ title, picurl, url, description }));
    messageInfo.title = messageInfo.picurl = messageInfo.url = messageInfo.description = "";
    if (state) {
      messageInfo._id = state.record._id;
      dispatch(actionCreators.updateConfigForm(messageInfo, props.history))
    } else {
      dispatch(actionCreators.createConfigForm(messageInfo, props.history))
    }
  },
  resetState() {
    dispatch(actionCreators.resetState());
  },
  initState(record, index) {
    dispatch(actionCreators.initState(record, index))
  }
});

export default connect(mapState, mapDispatch)(Form.create({name: 'editConfigInfo'})(EditConfigInfo));