import axios from '../../../../utils/http';
import * as constants from './constants';
import { Modal, message } from 'antd';
import backTop from '../../../../utils';

export const getGroupList = () => {
  return (dispatch) => {
    axios.get('/gonghaoTag')
      .then(res => {
        if (res.data.code === 1) {
          let gonghaoTagList = res.data.data;
          const action = {
            type: constants.GET_GONGHAO_TAG_LIST,
            gonghaoTagList
          };
          dispatch(action)
        }
      })
  }
};

export const getAllGonghaoList = () => {
  return dispatch => {
    axios.get('/conf')
      .then(res => {
        if (res.data.data.length > 0) {
          let allGonghaoList = res.data.data;
          const action = {
            type: constants.GET_ALL_GONGHAO_LIST,
            allGonghaoList,
            gonghaoList: allGonghaoList
          };
          dispatch(action);
        }
      })
  }
};

export const changePage = (page) => {
  return async dispatch => {
    const action = {
      type: constants.CHANGE_PAGE,
      page
    };
    await backTop();
    await dispatch(action);
  }
};


export const changeSearchInput = searchName => ({
  type: constants.CHANGE_SEARCH_INPUT,
  searchName
});

export const searchByName = nick_name => {
  return dispatch => {
    axios.get('/conf/find_one', {params: {nick_name: encodeURIComponent(nick_name)}})
      .then(res => {
        if (res.data.data.length > 0) {
          let allGonghaoList = res.data.data,
            gonghaoList = allGonghaoList;
          const action = {
            type: constants.SEARCH_BY_NAME,
            allGonghaoList, gonghaoList
          };
          dispatch(action);
          message.success("查询成功")
        }
      })
  }
};

export const changeSelectValue = (key, value) => ({
  type: constants.CHANGE_SELECT_VALUE,
  key, value
});

export const deleteGonghao = (id, index) => {
  return dispatch => {
    Modal.confirm({
      okText: "确定",
      cancelText: "取消",
      content: "确认删除吗？",
title: "提示",
      onOk: () => {
        axios.get("/conf/del", {
          params: {id}
        }).then(res => {
          const action = {
            type: constants.DELETE_GONGHAO,
            index
          };
          dispatch(action);
          message.success("删除成功")
        });
      }
    })
  }
};

