export const addNewGonghao = () => {
  return dispatch => {
    axios.get('/component/componentAuthorize')
      .then(res => {
        window.open(res.data)
      })
  }
};

export const openGonghaoModal = (id, group, attribute) => ({
  type: constants.OPEN_GONGHAO_MODAL,
  id, group, attribute
});

export const closeGonghaoModal = () => ({
  type: constants.CLOSE_GONGHAO_MODAL
});

export const updateGonghaoInfo = gonghaoInfo => {
  return dispatch => {
    console.log(gonghaoInfo)
    axios.put('/conf', {...gonghaoInfo})
      .then(res => {
        if(res.data.code === 1) {
          message.success(res.data.msg);
          dispatch(closeGonghaoModal());
        }
      })
  }
};

export const changeGroup = name => ({
  type: constants.CHANGE_GROUP,
  name
});

export const changeAttribute = attribute => ({
  type: constants.CHANGE_ATTRIBUTE,
  attribute
});

export const selectTag = (selectedTag) => ({
  type: constants.SESECT_TAG,
  selectedTag
});

export const removeTag = () => ({
  type: constants.REMOVE_TAG
});

export const tagConfirm = (group, props) => {
  return (dispatch) => {
    let name;
    axios.post("/gonghaoTag", {name: group})
      .then(res => {
        if (res.data.exist === 0) {
          name = res.data.data
        }
        props.form.setFieldsValue({group})
        const action = {
          type: constants.TAG_CONFIRM,
          name, group
        };
        dispatch(action)
      })
  }
};

export const deleteTagItem = (id, index) => {
  return (dispatch) => {
    axios.delete('/gonghaoTag/' + id)
      .then(res => {
        const action = {
          type: constants.DELETE_TAG_ITEM,
          index
        };
        dispatch(action)
      });
  }
};

export const createForm = (gonghaoInfo, history) => {
  return (dispatch) => {
    axios.post("/conf/create", gonghaoInfo).then(res => {
      history.goBack();
      const action = {
        type: constants.CREATE_FORM,
        gonghaoInfo
      };
      dispatch(action);
      message.success("添加成功")
    });
  }
};

export const updateForm = (gonghaoInfo, history) => {
  return (dispatch) => {
    axios.post("/conf/update", gonghaoInfo).then(res => {
      history.goBack();
      getAllGonghaoList();
      const action = {
        type: constants.UPDATE_FORM,
        gonghaoInfo
      };
      dispatch(action);
      message.success("修改成功")
    });
  }
};

export const checkPageAttr = () => ({
  type: constants.CHECK_PAGE_ATTR
});

