import React, { Component } from 'react';

import { Provider } from 'react-redux';
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";

import store from './store';

import Login from './pages/login';

import BasicLayout from './common/BasicLayout';
import TuoguanRouter from './pages/gonghao/router';
import MessageRouter from './pages/message/router';
import ChannelRouter from './pages/channel/router';
import AccountRouter from './pages/account/router';

class App extends Component{

  state = {
    userInfo: {}
  };

  componentDidMount() {
    let userInfo = localStorage.getItem("userInfo");
    if(userInfo && (Date.now() - (+ new Date(JSON.parse(userInfo).loginAt)) < 12 * 60 * 60 * 1000)) {
      this.setState({userInfo: JSON.parse(userInfo)});
    }
  }

  render() {
    const { userInfo } = this.state;
    const needLogin = Object.keys(userInfo).length > 0;
    return (
      <Provider store={store}>
        <BrowserRouter>
          <Switch>
            <Route path="/admin/login" render={() => needLogin ? <Redirect to={window.location.search ? decodeURIComponent(window.location.search.split("=")[1]) : "/admin"} /> : <Login />} />
            <Route path="/admin" render={() => (
              needLogin ?
                <BasicLayout content={
                    <Switch>
                      <Route path='/admin/gonghao' component={TuoguanRouter} />
                      <Route path='/admin/message' component={MessageRouter} />
                      <Route path='/admin/channel' component={ChannelRouter} />
                      <Route path='/admin/account' component={AccountRouter} />
                      <Redirect from="/admin" exact to={userInfo.role === 2 ? "/admin/gonghao" : "/admin/account"} />
                    </Switch>
                } />
                : <Redirect to={"/admin/login?redirect=" + encodeURIComponent(window.location.pathname)} />
            )} />
            <Redirect from="/" exact to="/admin" />
          </Switch>
        </BrowserRouter>
      </Provider>
    );
  }
}

export default App;
