import * as constants from './constants';

const resetFields = {
  type: 0,
  sex: 3,
  attribute: 1,
  replyType: 0,
  codes: [],
  articles: []
};

const defaultState = {
  // 公号配置
  replyMessageList: [],
  messageInfo: JSON.parse(JSON.stringify(resetFields)),
  allCodes: [],
  selectGonghaoModal: true,
  updateIndex: -1,
  messageIndex: -1
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    // 公号配置
    case constants.GET_REPLY_MESSAGE_LIST:
      newState.replyMessageList = action.replyMessageList.filter(item => item.type !== 1);
      return newState;
    case constants.DELETE_REPLY_MESSAGE:
      newState.replyMessageList.splice(action.index, 1);
      return newState;
    case constants.GET_ALL_CODES:
      newState.allCodes = action.allCodes;
      return newState;
    case constants.CLOSE_SELECT_MODAL:
      newState.selectGonghaoModal = false;
      return newState;
    case constants.OPEN_SELECT_MODAL:
      newState.selectGonghaoModal = true;
      return newState;
    case constants.CHANGE_MESSAGE_INFO:
      newState.messageInfo[action.key] = action.value;
      return newState;
    case constants.ADD_TO_LIST:
      const { title, picurl, url, description } = newState.messageInfo;
      newState.messageInfo.articles.push({...{ title, picurl, url, description }});
      newState.messageInfo.title = newState.messageInfo.picurl = newState.messageInfo.url = newState.messageInfo.description = "";
      return newState;
    case constants.UPDATE_MESSAGE_ITEM:
      const { item, index} = action;
      Object.assign(newState.messageInfo, {...item});
      newState.updateIndex = index;
      return newState;
    case constants.UPDATE_CURRENT_MESSAGE:
      let message = {
        title: newState.messageInfo.title,
        picurl: newState.messageInfo.picurl,
        url: newState.messageInfo.url,
        description: newState.messageInfo.description
      };
      newState.messageInfo.articles.splice(newState.updateIndex, 1, {...message});
      newState.messageInfo.title = newState.messageInfo.picurl = newState.messageInfo.url = newState.messageInfo.description = "";
      newState.updateIndex = -1;
      return newState;
    case constants.DELETE_CURRENT_MESSAGE:
      newState.messageInfo.articles.splice(newState.updateIndex, 1);
      newState.messageInfo.title = newState.messageInfo.picurl = newState.messageInfo.url = newState.messageInfo.description = "";
      newState.updateIndex = -1;
      return newState;

    case constants.INIT_STATE:
      newState.messageInfo = JSON.parse(JSON.stringify(action.record));
      newState.messageIndex = action.index;
      if(newState.messageInfo.replyType === 1) {
        Object.assign(newState.messageInfo, newState.messageInfo.articles[0]);
        newState.updateIndex = 0;
      }
      return newState;

    case constants.RESET_STATE:
      newState = JSON.parse(JSON.stringify(defaultState));
      return newState;

    default:
      return state;
  }
};