import React from 'react';
import {Card, List, Icon, Tooltip} from 'antd';
import "./index.scss";
import moment from 'moment';

const GraphicMessage = props => {
  const {messageList, openSyncModal, openModal, openTimingModal, copy, deleteMaterial, openUpdateMaterialModal} = props;
  return (
    <div className="message-list-wrapper">
      <List grid={{gutter: 20, sm: 3, xxl: 6}} dataSource={messageList}
            renderItem={(item, index) => <List.Item key={item._id}>
              <Card className="message-list-card" title={
                <List className="message-list">
                  <div className="update-time"><Icon
                    type="sound"/>{item.isTiming ? `于 ${moment(item.timing).format("YYYY-MM-DD HH:mm:ss")} 发送` : "未设置定时时间，请手动发送"}</div>
                  <Tooltip title="" key={item.content.news_item[0].thumb_media_id + "0"}>
                    <List.Item className="cursor" onClick={() => openUpdateMaterialModal(item.media_id, 0, item.content.news_item[0], item._id)}>
                      <div className="head-message">
                        <p className="ellipse-two">{item.content.news_item[0].title}</p>
                        <img src={item.content.news_item[0].local_img_path} alt="请先在微信后台上传图片"/>
                      </div>
                    </List.Item>
                  </Tooltip>
                  {item.content.news_item.slice(1).map((message, i) => <Tooltip title="点击修改原文链接" key={message.thumb_media_id + i + 1} placement="rightTop">
                    <List.Item className="border_btm cursor" onClick={() => openUpdateMaterialModal(item.media_id, i + 1, item.content.news_item[i + 1], item._id)}>
                      <div className="rest-message">
                        <p className="ellipse-two">{message.title}</p>
                        <img src={message.local_img_path} alt="miss"/>
                      </div>
                    </List.Item>
                  </Tooltip>)}
                </List>}
                    actions={[
                      <Tooltip title="同步"><Icon onClick={() => openSyncModal(item)} type="cloud-upload"
                                                key="sync"/></Tooltip>,
                      <Tooltip title="发送"><Icon onClick={() => openModal(item)} type="message" key="send"/></Tooltip>,
                      <Tooltip title="定时发送"><Icon onClick={() => openTimingModal(item)} type="history" key="timing"/></Tooltip>,
                      <Tooltip title="复制"><Icon onClick={() => copy(item._id)} type="fork" key="copy"/></Tooltip>,
                      <Tooltip title="删除"><Icon onClick={() => deleteMaterial(item._id, index)} type="delete"
                                                key="del"/></Tooltip>,
                    ]}
              />
            </List.Item>}/>

    </div>
  )
};

export default GraphicMessage;