import React, {Component} from 'react';
import {Avatar, Input, List, Layout, Collapse, message} from "antd";
import axios from 'axios';
import "./index.scss";

const {Sider, Content} = Layout;
const {Panel} = Collapse;

class SingleSelectByGroup extends Component {

  state = {
    gonghaoTagList: [],
    gonghaoList: [],
    nick_name: "",
    _id: "",
    activeKey: ""
  };

  search = nick_name => {
    const {gonghaoTagList} = this.state;
    if (nick_name !== "") {
      axios.get("/conf/find_one", {params: {nick_name: encodeURIComponent(nick_name)}})
        .then(res => {
          if (res.data.data.length !== 0) {
            const {data} = res.data;
            this.props.setCode(data[0].code);
            let selectGonghao = gonghaoTagList.filter(item => data[0].group === item.name);
            this.setState({nick_name: "", _id: data[0]._id, activeKey: selectGonghao[0].name})
          } else {
            message.info("没有查询到包含该字符的公号，请检查后重新输入")
          }
        })
    } else {
      message.info("公号名不能为空")
    }
  };

  changeSearchInput = e => {
    let nick_name = e.target.value;
    this.setState({nick_name})
  };

  selectGonghao = gonghao => {
    const {code, _id} = gonghao;
    this.props.setCode(code);
    this.setState({_id})
  };

  handleChangeGroup = value => {
    this.setState({activeKey: value || ""})
  };

  render() {
    const {gonghaoTagList, gonghaoList, nick_name, _id, activeKey} = this.state;
    const PanelList = name => (
      <List className="gonghao-list" dataSource={gonghaoList.length > 0 ? gonghaoList.filter(i => i.group === name) : null} locale={{emptyText: <span style={{display: "none"}}>123</span>}}
        renderItem={gonghao =>
          <List.Item className={gonghao._id === _id ? "cursor selected" : "cursor"} onClick={() => this.selectGonghao(gonghao)} key={gonghao._id}>
            <Avatar shape="square" size={22} icon="user"/>{gonghao.nick_name}
        </List.Item>
      }/>
    );
    return (
      <Layout style={{background: "#fff"}} className="message-group-wrapper">
        <Sider className="message-sider-wrapper" theme="light" width="180">
          <Input.Search
            className="search-input"
            placeholder="请输入公号名称"
            onSearch={this.search}
            value={nick_name}
            onChange={this.changeSearchInput}
          />
          <List>
            <List.Item>
              <Collapse bordered={false} accordion activeKey={activeKey} onChange={this.handleChangeGroup}>
                <Panel header="未分组" key="未分组">
                  {PanelList("未分组")}
                </Panel>
                {gonghaoTagList.map(item =>
                  <Panel header={item.name} key={item.name}>
                    {PanelList(item.name)}
                  </Panel>
                )}
              </Collapse>
            </List.Item>
          </List>
        </Sider>
        <Content>{this.props.content}</Content>
      </Layout>
    )
  }

  getGonghaoTagList = () => {
    axios.get("/gonghaoTag")
      .then(res => {
        if (res.data.code === 1) {
          const {data: gonghaoTagList} = res.data;
          this.setState({gonghaoTagList})
        }
      })
  };

  getGonghaoList = () => {
    axios.get('/conf')
      .then(res => {
        const {data, code} = res.data;
        if (code === 1) {
          this.setState({gonghaoList: data})
        }
      })
  };

  componentDidMount() {
    this.getGonghaoTagList();
    this.getGonghaoList();
  }
}

export default SingleSelectByGroup;