import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { Avatar, Popover, Icon, message, Modal, Form, Input, Tooltip } from 'antd';
import axios from 'axios';

import './index.scss';

class HeaderWrapper extends Component {

  state = {
    userInfo: {}
  };

  componentDidMount() {
    let userInfo = localStorage.getItem("userInfo");
    if(userInfo) {
      userInfo = JSON.parse(userInfo);
      this.setState({userInfo})
    }
  }

  render() {
    const { userInfo, showUpdateModal } = this.state;
    const { getFieldDecorator } = this.props.form;
    return (
      <div className='header-wrapper'>
        <Link to="/">
          <img className="logo fl" src={require("../../../assets/images/logo.png")} alt=""/>
        </Link>
        <div className="user-info fr">
          <Tooltip title="帮助文档">
            <a href="http://t.dmmup.com/平台接管及发文方式.pdf" target="__blank">
              <Avatar size={25} icon="question" style={{marginRight: 10, color: '#f56a00', backgroundColor: '#fde3cf' }} />
            </a>
          </Tooltip>
          <Avatar size={25} icon="user" />
          <Popover content={
            <>
              <p style={{fontSize: 12, marginBottom: 10}} className="cursor" onClick={() => this.setState({showUpdateModal: true})}><Icon style={{marginRight: 5}} type="edit" />修改信息</p>
              <p style={{fontSize: 12}} className="cursor" onClick={logout}><Icon style={{marginRight: 5}} type="logout" />退出</p>
            </>
          }>
            <div className="user fr cursor">
              <p>{userInfo.username},</p>
              <p>欢迎回来<Icon type="down" /></p>
            </div>
          </Popover>
          <Modal visible={showUpdateModal} title="修改用户信息" onOk={this.updateInfo} onCancel={this.cancelUpdate}>
            <Form labelCol={{span: 4}} wrapperCol={{span: 20}}>
              <Form.Item label="用户名：">
                {getFieldDecorator('username', {
                  initialValue: userInfo.username
                })(
                  <Input placeholder="请输入用户名"/>
                )}
              </Form.Item>
              <Form.Item label="新密码：">
                {getFieldDecorator('password', {
                  rules: [{
                    required: true,
                    message: "登录密码不能为空"
                  }]
                })(
                  <Input type="password" placeholder="请输入新密码"/>
                )}
              </Form.Item>
            </Form>
          </Modal>
        </div>
      </div>
    )
  }

  updateInfo = () => {
    const { getFieldsValue } = this.props.form;
    const { userInfo: {_id: id} } = this.state;
    const { username, password } = getFieldsValue();
    if(password.trim() && username.trim()) {
      Modal.confirm({
        title: "确定修改用户信息吗？",
        onOk: () => {
          let md5Password = this.$MD5(password).toString();
          axios.put('/account', {id, username, password: md5Password })
            .then(res => {
              const {code, msg} = res.data;
              if(code === 1) {
                message.success(msg + "请重新登录");
                this.cancelUpdate();
                localStorage.setItem("userInfo", "");
                window.location.href = "/admin/login";
              } else {
                message.error(msg);
              }
            })
        }
      })
    } else {
      message.error("请检查输入是否有误，密码首尾不能为空格")
    }
  };

  cancelUpdate = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({showUpdateModal: false});
    setFieldsValue({password: ""});
  }

}

const updateInfo = () => {

};

const logout = () => {
  localStorage.setItem("userInfo", "");
  message.success("退出登录成功");
  setTimeout(() => {
    window.location.href = "/admin/login";
  }, 500)
};

export default Form.create()(HeaderWrapper);
