import React, { Component } from 'react';
import { Table, Button } from 'antd';
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import backTop from '../../../utils';

import {actionCreators} from "./store";

class Menu extends Component {
  render() {
    const { menuList, deleteMenu } = this.props;
    const menuHeader =  [
      {
        title: "菜单类型",
        key: "individual",
        dataIndex: "individual",
        align: "center",
        render: (text, record) => record.individual ? "个性化菜单" : "通用菜单"
      },
      {
        title: "菜单性别",
        key: "sex",
        dataIndex: "sex",
        align: "center",
        render: (text, record) => (record.individual ? (record.sex === '0' ? "未知"
          : (record.sex === '1' ? '男' : '女')) : '——')
      },
      {
        key: "title",
        dataIndex: "title",
        title: "标题",
        align: "center"
      },
      {
        title: "Action",
        key: "Action",
        align: "center",
        render: (text, record, index) => (
          <div>
            <Button size="small" type="primary" style={{marginRight: "10px"}}>
              <Link to={{pathname: "/admin/gonghao/menu/update", query: record}}>编辑</Link>
            </Button>
            <Button size="small" type="danger" onClick={() => deleteMenu(record._id, index)}>删除</Button>
          </div>
        )
      }
    ];

    return (
    	<div>
    		<div className="container">
          <Button type="primary" style={{marginBottom: "20px"}}>
            <Link to="/admin/gonghao/menu/create">配置新菜单栏</Link>
          </Button>
          <Table size="small" rowKey={record => record._id} columns={menuHeader} dataSource={menuList} pagination={{onChange: () => backTop()}} />
        </div>
    	</div>
    )
  }
  componentDidMount() {
    this.props.getMenuList()
  }
}

const mapState = state => ({
  menuList: state.gonghao.menu.menuList
});

const mapDispatch = dispatch => ({
  getMenuList() {
    dispatch(actionCreators.getMenuList())
  },
  deleteMenu(id, index) {
    dispatch(actionCreators.deleteMenu(id, index))
  }
});

export default connect(mapState, mapDispatch)(Menu);