import { combineReducers } from 'redux';
import { reducer as tuoguanReducer } from './tuoguan/store';
import { reducer as menuReducer } from './menu/store';
import { reducer as replyReducer } from './reply/store';

const reducer = combineReducers({
  tuoguan: tuoguanReducer,
  menu: menuReducer,
  reply: replyReducer,
});

export default reducer;