import React, { Component } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Spread from './spread';
import EditSpread from './spread/edit';

class ChannelWrapper extends Component {
  render() {
    return (
        <Route path="/admin/channel" render={
          () =>
            <Switch>
              <Route path="/admin/channel/spread/index" component={Spread} />
              <Route path="/admin/channel/spread/create" component={EditSpread} />
              <Route path="/admin/channel/spread/update" component={EditSpread} />
              <Redirect from="/admin/channel" exact to='/admin/channel/spread' />
              <Redirect from="/admin/channel/spread" exact to='/admin/channel/spread/index' />
            </Switch>
          }
        />
    )
  }
}

export default ChannelWrapper;