import React, {Component} from 'react';
import {Button, Table} from "antd";
import {connect} from "react-redux";
import {Link} from 'react-router-dom';
import {actionCreators} from './store';
import './index.scss';

class Config extends Component {

  render() {
    const {replyMessageList, allCodes, deleteReplyMessage} = this.props;
    const configHeader = [
      {
        key: "type",
        title: "类型",
        dataIndex: "type",
        align: "center",
        render: (text, record, index) => {
          return (
            <span>
              {record.type === 0 ? "文本"
                : (record.type === 1 ? "点击"
                  : (record.type === 2 ? "关注"
                    : (record.type === 4 ? "默认回复" : "~~")))}
            </span>
          )
        }
      }, {
        key: "name",
        dataIndex: "name",
        title: "消息名称",
        align: "center"
      },{
        key: "sex",
        title: "性别",
        dataIndex: "sex",
        align: "center",
        render: (text, record, index) => {
          return (
            <span>
              {record.sex === 1 ? "男" : (record.sex === 2
                ? "女" : (record.sex === 0 ? "未知" : "默认"))}
            </span>
          )
        }
      }, {
        key: "text",
        title: "用户回复文本",
        dataIndex: "text",
        align: "center",
        render: text => text ? text : "——"
      }, {
        key: "key",
        title: "点击关键词",
        dataIndex: "key",
        align: "center",
        render: text => text ? text : "——"
      }, {
        key: "codes",
        title: "公号名称",
        dataIndex: "codes",
        align: "center",
        render: text => text.length > 0 ? text.slice(0, 4).map(item => {
          let a = allCodes.filter(i => i.code === item)[0];
          return a && a.nick_name
        }) : "无"
      }, {
        key: "Action",
        title: "Action",
        align: "center",
        width: 120,
        render: (text, record, index) => {
          return (
            <div className="btn-wrapper">
              <Button size="small" type="primary">
                <Link to={{pathname: './update', state: {record, index}}}>编辑</Link>
              </Button>
              <Button size="small" style={{background: "red", marginLeft: "5px"}}
                      onClick={() => deleteReplyMessage(record._id, index)}>删除</Button>
            </div>
          )
        }
      },
    ];
    return (
      <div className="container">
        <div className="edit-config">
          <Button type="primary" className="edit-btn">
            <Link to='./create'>新建回复规则</Link>
          </Button>
          <Table className='config-table' size="small" rowKey={(record => record._id)} columns={configHeader} dataSource={replyMessageList} bordered pagination={false}/>
        </div>
      </div>
    )
  }

  componentDidMount() {
    this.props.getReplyMessageList();
    this.props.getAllCodes();
  }
}

const mapState = state => ({
  allCodes: state.gonghao.reply.allCodes,
  replyMessageList: state.gonghao.reply.replyMessageList
});

const mapDispatch = dispatch => ({
  getAllCodes() {
    dispatch(actionCreators.getAllCodes())
  },
  getReplyMessageList() {
    dispatch(actionCreators.getReplyMessageList())
  },
  deleteReplyMessage(id, index) {
    dispatch(actionCreators.deleteReplyMessage(id, index))
  }
});

export default connect(mapState, mapDispatch)(Config);