import React from 'react';
import {Modal, Input, Button} from 'antd';
import {connect} from 'react-redux';
import {actionCreators} from "../store";

const colors = ["#000", "#CF021B", "#F5A623", "#3BA6FF", "#BD10E0", "#999"];
const colorWrapperStyle = {
  width: 20,
  height: 20,
  marginLeft: 10,
  display: "inline-block",
  marginTop: 5
};

const TemplateModal = props => {
  const { show, content, closeTemplateModal, saveTemplateModal, editTemplate } = props;
  const footer = (
    <div style={{overflow: "hidden", lineHeight: 1.5}}>
      <div className="fl">
        {colors.map(item => <span className="cursor" style={{...colorWrapperStyle, background: item}} key={item} onClick={() => editTemplate('color', item)} />)}
      </div>
      <Button type="primary" className="fr" onClick={saveTemplateModal}>保存</Button>
    </div>);
  return(
    <Modal
      visible={show}
      onCancel={closeTemplateModal}
      closable={false}
      footer={footer}
      mask={false}
    >
      <Input.TextArea style={{color: content["color"]}} value={content["value"]} autosize={{ minRows: 3, maxRows: 6 }} onChange={e => editTemplate('value', e.target.value)} />
    </Modal>
  )
};

const mapState = state => ({
  content: state.message.template.content,
});

const mapDispatch = dispatch => ({
  closeTemplateModal() {
    dispatch(actionCreators.closeTemplateModal())
  },
  saveTemplateModal() {
    dispatch(actionCreators.saveTemplateModal())
  },
  editTemplate(key, value) {
    dispatch(actionCreators.editTemplate(key, value))
  }
});

export default connect(mapState, mapDispatch)(TemplateModal);