import axios from 'axios';
import * as constants from './constants';
import { Modal, message } from 'antd';

// 群发公号
const getGonghaoList = () => {
  return new Promise((resolve, reject) => {
    axios.get('/conf')
      .then(res => {
        if (res.data.data.length > 0) {
          let gonghaoList = res.data.data;
          const action = {
            type: constants.GET_GROUP_GONGHAO,
            gonghaoList
          };
          resolve(action)
        } else {
          reject('error message: 没有数据')
        }
      })
  });
};

export const getGroupGonghao = () => {
  return async dispatch => {
    let action = await getGonghaoList();
    dispatch(action);
  }
};

export const getMaterial = code => {
  return dispatch => {
    if(code) {
      axios.get('/material', {params: {code}})
        .then(res => {
          message.success(res.data.success);
          setTimeout(function () {
            dispatch(getMessageList(code))
          }, 3000)
        })
    } else {
      message.warning("请选择公号后再试")
    }
  }
};

export const openUpdateMaterialModal = updateInfo => ({
  type: constants.OPEN_UPDATE_MATERIAL_MODAL,
  updateInfo
});

export const closeUpdateMaterialModal = () => ({
  type: constants.CLOSE_UPDATE_MATERIAL_MODAL
});

export const updateMaterial = (updateInfo, code) => {
  return dispatch => {
    axios.put("/material/contentSourceUrl", {...updateInfo, code})
      .then(res => {
        const { code, msg } = res.data;
        if(code === 1) {
          message.success(msg);
          dispatch(closeUpdateMaterialModal())
        } else {
          message.error(msg)
        }
      })
      .catch(error => {
        console.log(error)
      })
  }
};

export const changeContentSourceUrl = content_source_url => ({
  type: constants.CHANGE_CONTENT_SOURCE_URL,
  content_source_url
});

// 群发素材

export const getMessageList = code => {
  return dispatch => {
    axios
      .get("/material/show", { params: { code: Number(code) } })
      .then(res => {
        const { data } = res.data;
        const action = {
          type: constants.GET_GROUP_MATERIAL,
          data
        };
        dispatch(action)
      });
  }
};

export const getTagList = code => {
  return dispatch => {
    axios.get("/material/tag", {params: {code}})
      .then(res => {
        const { data } = res.data;
        const action = {
          type: constants.GET_TAG_LIST,
          data
        };
        dispatch(action);
      })
  }
};

export const changeTagId = (value, isTiming) => ({
  type: constants.CHANGE_TAG_ID,
  value, isTiming
});

export const closeModal = () => ({
  type: constants.CLOSE_MODAL
});

export const openModal = record => ({
  type: constants.OPEN_MODAL,
  record
});

export const sendMessage = sendInfo => {
  return dispatch => {
    Modal.confirm({
      title: "确定发送消息吗？",
      onOk: () => {
        axios.get("/material/sendMsg", {params: {...sendInfo}})
          .then(res => {
            message.info(res.data.success);
            dispatch(closeModal());
          });
      }
    })
  }
};

export const openTimingModal = record => ({
  type: constants.OPEN_TIMING_MODAL,
  record
});

export const sendTimingMessage = (sendTimingInfo, code) => {
  return dispatch => {
    Modal.confirm({
      title: "确定定时发送消息吗？",
      onOk: () => {
        axios.get("/material/send_timing", {params: {...sendTimingInfo}})
          .then(res => {
            message.info("设置成功");
            dispatch(getMessageList(code));
            dispatch(closeModal());
          });
      }
    })
  }
};

export const changeTimingState = value => ({
  type: constants.CHANGE_TIMING_STATE,
  value
});

export const changeTime = (time, timeType) => ({
  type: constants.CHANGE_TIME,
  time, timeType
});

export const copy = id => {
  return dipatch => {
    Modal.confirm({
      title: "确定复制这条素材吗？",
      onOk: () => {
        axios.post("/material/copy", {id})
          .then(res => {
            if (res.data.success) {
              const { data } = res.data;
              message.success(res.data.success);
              const action = {
                type: constants.COPY,
                data
              };
              dipatch(action)
            } else {
              message.info(res.data.err);
            }
          })
      }
    })
  }
};

export const deleteMaterial = (id, index) => {
  return dispatch => {
    Modal.confirm({
      title: "确定删除这条素材吗？",
      onOk: () => {
        axios.get("/material/del", {params: {id}})
          .then(res => {
            if (res.data.success) {
              message.success(res.data.success);
              const action = {
                type: constants.DELETE_MATERIAL,
                 index
              };
              dispatch(action);
            } else {
              message.info(res.data.err);
            }
          })
      }
    })
  }
};

export const clearMaterial = code => {
  return dispatch => {
    if(code) {
      Modal.confirm({
        title: "确定清空列表吗？",
        onOk: () => {
          axios.get("/material/clear", { params: { code } })
            .then(res => {
              message.info(res.data.success);
              const action = {
                type: constants.CLEAR_MATERIAL
              };
              dispatch(action);
            });
        }
      })
    } else {
      message.warning("请选择公号后再试")
    }
  }
};

