import React, {Component, Fragment} from 'react';
import {Form, Input, Button, Switch, InputNumber, message, Modal, List, Tag } from 'antd';
import axios from 'axios';

const initState = {
  spreadLinkInfo: {
    id: "",
    title: "",
    links: [],
    weights: [],
    back_urls: [],
    link: "",
    back_url: "",
    weight: "",
    type: false,
    status: false,
    granularity: 1,
    group: "",
    remarks: ""
  },
  tagList: [],
  index: -1
};

class EditAdvertise extends Component {
  constructor(props) {
    super(props);
    this.addTag = this.addTag.bind(this);
    this.cancelSelectTag = this.cancelSelectTag.bind(this);
    this.cancelEdit = this.cancelEdit.bind(this);
    this.submitSpreadLink = this.submitSpreadLink.bind(this);
    this.cancelUpdate= this.cancelUpdate.bind(this);
  }

  state = {...initState};

  componentWillMount() {
    let query = this.props.history.location.query;
    query && this.initData(query);
    this.getTagList();
  }

  render() {
    const {spreadLinkInfo, tagList, index} = this.state;
    const {id, title, links, weights, back_urls, link, back_url, weight, type, status, granularity, group, remarks} = spreadLinkInfo;
    let isAddWeight, hasLink, hasBackUrl, hasWeight, selectedTag;
    links.length > 0 && (hasLink = <Form.Item label="链接组">
      <List
        size="small"
        bordered
        dataSource={links}
        renderItem={(item, index) => (
          <List.Item actions={[<span className="cursor" style={{color: "#f00"}} onClick={() => this.delLink(index, "links")}>del</span>]}>
            {item}
          </List.Item>
        )}
      />
    </Form.Item>);
    back_urls.length > 0 && (hasBackUrl = <Form.Item label="返回链接组">
      <List
        size="small"
        bordered
        dataSource={back_urls}
        renderItem={(item, index) => (
          <List.Item actions={[<span className="cursor" style={{color: "#f00"}} onClick={() => this.delLink(index, "back_urls")}>del</span>]}>
            {item}
          </List.Item>
        )}
      />
    </Form.Item>);
    weights.length > 0 && (hasWeight = <Form.Item label="权重组">
      <List
        size="small"
        bordered
        dataSource={weights}
        renderItem={(item, i) => {
          let isUpdate;
          if(index === i) {
            isUpdate = [<span className="cursor" style={{color: "#f00"}} onClick={this.cancelUpdate}>取消</span>]
          } else {
            isUpdate = [<span className="cursor" style={{color: "#f00"}} onClick={e => this.delLink(i, "weights", e)}>删除</span>]
          }
          return (
          <List.Item onClick={() => this.updateWeight(i)} actions={isUpdate}>
            {index === i ? <Input.Search size="small" enterButton="保存" onSearch={(value, e) => this.saveUpdate(value, e)} /> : item}
          </List.Item>
        )}}
      />
    </Form.Item>);
    if (type) {
      isAddWeight = <Fragment>
        <Form.Item label="权重">
          <Input.Search value={weight} enterButton="添加" placeholder="请输入权重"
                        onChange={e => this.changeInputValue(e.target.value, "weight")} onSearch={value => this.addLink(value, "weights", "weight")}/>
        </Form.Item>
        {hasWeight}
        <Form.Item label="投放状态">
          <Switch checked={status} onChange={value => this.changeInputValue(value, "status")}/>
        </Form.Item>
        <Form.Item label="时间颗粒度">
          <InputNumber min={1} value={granularity} placeholder="请输入标题"
                       onChange={value => this.changeInputValue(value, "granularity")}/>
        </Form.Item>
      </Fragment>
    }
    if(group) {
      selectedTag = <Tag closable onClose={this.cancelSelectTag}>{group}</Tag>
    } else {
     selectedTag = <Input.Search enterButton="确定" placeholder="请输入标签" onSearch={this.addTag}/>
    }
    return (
      <div>
        <div className="container">
          <div className="title-wrapper">
            <h2>编辑推广链接</h2>
            <div className="fr">
              <Button type="primary" style={{marginRight: 10}} onClick={this.submitSpreadLink}>提交</Button>
              <Button onClick={this.cancelEdit}>取消</Button>
            </div>
          </div>
          <Form className="fl" style={{width: "60%", borderRight: "1px solid #eee"}} labelCol={{sm: 3}} wrapperCol={{sm: 18, offset: 0}} colon={false}>
            <Form.Item label="标题">
              <Input value={title} placeholder="请输入标题"
                     onChange={e => this.changeInputValue(e.target.value, "title")}/>
            </Form.Item>
            <Form.Item label="链接">
              <Input.Search value={link} enterButton="添加" placeholder="请输入链接" onChange={e => this.changeInputValue(e.target.value, "link")} onSearch={value => this.addLink(value, "links", "link")}/>
            </Form.Item>
            {hasLink}
            <Form.Item label="返回链接">
              <Input.Search value={back_url} enterButton="添加" placeholder="请输入返回链接" onChange={e => this.changeInputValue(e.target.value, "back_url")} onSearch={value => this.addLink(value, "back_urls", "back_url")}/>
            </Form.Item>
            {hasBackUrl}
            <Form.Item label="选择标签">
              {selectedTag}
            </Form.Item>
            <Form.Item label="标签选项">
              {tagList.map((item, index) => <Tag className="cursor" closable onClose={(e) => this.deleteTag(e, item._id, index)} key={item._id} onClick={() => this.selectTag(item.name)}>{item.name}</Tag>)}
            </Form.Item>
          </Form>
          <Form className="fl" style={{width: "36%", paddingLeft: "4%"}} labelCol={{sm: 7}} wrapperCol={{sm: 17, offset: 0}} colon={false}>
            <Form.Item label="是否增加权重">
              <Switch checked={type} onChange={value => this.changeInputValue(value, "type")}/>
            </Form.Item>
            {isAddWeight}
            <Form.Item label="链接备注">
              <Input value={remarks} placeholder="请输入备注" onChange={e => this.changeInputValue(e.target.value, "remarks")}/>
            </Form.Item>
          </Form>
        </div>
      </div>
    )
  }

  initData(query) {
    query.type = query.type === 1;
    query.status = query.status === 1;
    this.setState(state => ({spreadLinkInfo: {...state.spreadLinkInfo, ...query}}))
  }

  changeInputValue(value, name) {
    let {spreadLinkInfo} = this.state;
    spreadLinkInfo[name] = value;
    this.setState({spreadLinkInfo})
  }

  addLink(value, list, item) {
    const {spreadLinkInfo} = this.state;
    if(!value.trim()) {
      message.error("链接不能为空")
    } else {
      spreadLinkInfo[list].push(value.trim());
      spreadLinkInfo[item] = "";
      this.setState(state => ({spreadLinkInfo: {...state.spreadLinkInfo, ...spreadLinkInfo}}))
    }
  }

  delLink(index, list, e) {
    e && e.stopPropagation();
    const {spreadLinkInfo} = this.state;
    spreadLinkInfo[list].splice(index, 1);
    this.setState(state => ({spreadLinkInfo: {...state.spreadLinkInfo, ...spreadLinkInfo}}))
  }

  updateWeight(index) {
    this.setState({index})
  }

  saveUpdate(value, e) {
    e.stopPropagation();
    const {spreadLinkInfo, index} = this.state;
    spreadLinkInfo["weights"].splice(index, 1, value);
    this.setState(state => ({spreadLinkInfo: {...state.spreadLinkInfo, ...spreadLinkInfo}, index: -1}));
  }

  cancelUpdate(e) {
    e.stopPropagation();
    this.setState({index: -1})
  }

  getTagList() {
    axios.get("/tuiguangTag").then(res => {
      let tagList = res.data.data;
      this.setState({tagList});
    });
  }

  addTag(value) {
    const { spreadLinkInfo } = this.state;
    if(!value.trim()) {
      message.error("标签名不能为空")
    } else {
      axios.post("/tuiguangTag", {name: value.trim()})
        .then(res => {
          if (res.data.exist === 0) {
            const { data } = res.data;
            this.setState(state => ({tagList: [...state.tagList, data]}));
          }
          spreadLinkInfo.group = value;
          this.setState(state => ({spreadLinkInfo: {...state.spreadLinkInfo, ...spreadLinkInfo}}));
        })
    }
  }

  selectTag(group) {
    const {spreadLinkInfo} = this.state;
    spreadLinkInfo.group = group;
    this.setState(state => ({spreadLinkInfo: {...state.spreadLinkInfo, ...spreadLinkInfo}}));
  }

  cancelSelectTag() {
    const {spreadLinkInfo} = this.state;
    spreadLinkInfo.group = "";
    this.setState(state => ({spreadLinkInfo: {...state.spreadLinkInfo, ...spreadLinkInfo}}));
  }

  deleteTag(e, id, index) {
    e.stopPropagation();
    const {tagList} = this.state;
    axios.delete('/tuiguangTag', {params: {id}})
      .then(res => {
        tagList.splice(index, 1)
        this.setState({tagList})
      });
  }

  submitSpreadLink() {
    const { query } = this.props.history.location;
    const {spreadLinkInfo} = this.state;
    spreadLinkInfo.type = spreadLinkInfo.type ? 1 : 0;
    spreadLinkInfo.status = spreadLinkInfo.status ? 1 : 0;
    let url = '/transfer/create';
    if(query) {
      url = '/transfer/update'
    }
    Modal.confirm({
      title: "确定提交吗？",
      onOk: () => {
        axios.post(url, {...spreadLinkInfo})
          .then(res => {
            if (res.data.success) {
              message.info(res.data.success);
              this.cancelEdit()
            } else {
              message.info(res.data.err);
            }
          });
      }
    })
  }

  cancelEdit() {
    this.props.history.goBack();
    this.resetState();
  }

  resetState() {
    this.setState({...initState})
  }

  componentWillUnmount() {
    this.resetState()
  }
}

export default EditAdvertise;