import React, { Component } from 'react';
import { Input, Table, message, Modal } from 'antd';
import Button from '../../common/components/Button';
import axios from 'axios';
import { CopyToClipboard } from 'react-copy-to-clipboard';

const Search = Input.Search;

class Account extends Component {

  state = {
    accountList: [],
    searchValue: "",
    updateRemarksIndex: -1
  };



  searchAccount = () => {
    const { searchValue } = this.state;
    axios.get('/account', {params: {username: searchValue}})
      .then(res => {
        if(res.data.code === 1) {
          const {data: accountList, msg} = res.data;
          this.setState({accountList, searchValue: ""});
          message.success(msg)
        } else {
          message.error(res.data.msg);
          this.getAccountList()
        }
      })
  };

  saveUpdate(id, remarks) {
    Modal.confirm({
      title: "确定保存修改吗？",
      onOk: () => {
        axios.put("/account/remarks", {id, remarks})
          .then(res => {
            const {code, msg} = res.data;
            if(code === 1) {
              message.success(msg);
              this.setState({
                updateRemarksIndex: -1
              });
              this.getAccountList()
            } else {
              message.error(msg);
            }
          })
      }
    })
  }

  deleteAccount(_id) {
    Modal.confirm({
      title: "确定删除这个账户吗？",
      onOk: () => {
        axios.delete('/account', {params: {_id}})
          .then(res => {
            if(res.data.code === 1) {
              message.success(res.data.msg);
              this.getAccountList()
            } else {
              message.error(res.data.msg);
            }
          })
      }
    })
  }

  getAccountList = () => {
    axios.get('/account')
      .then(res => {
        if(res.data.code === 1) {
          let { data } = res.data;
          let accountList = data.reverse();
          this.setState({accountList});
        }
      })
  };

  componentDidMount() {
    this.getAccountList()
  }

  handleChangeSearchValue = e => {
    this.setState({searchValue: e.target.value})
  };

  render() {
    const {accountList, searchValue, updateRemarksIndex} = this.state;
    const columns = [
      {
        key: 'username',
        dataIndex: 'username',
        title: '账户名',
        align: 'center'
      }, {
        key: 'remarks',
        dataIndex: 'remarks',
        title: '公司名称/备注',
        align: 'center',
        width: 300,
        render: (text, record, index) => updateRemarksIndex !== index ? text : <Input.Search enterButton="保存" size="small" onSearch={value => this.saveUpdate(record._id,value)} />
      }, {
        key: 'password',
        dataIndex: 'password',
        title: '密码（点击自动复制，打开解密网站）',
        align: 'center',
        render: text => <CopyToClipboard text={text} onCopy={() => message.success("复制成功")}><a href="https://www.somd5.com/" target="__blank">{text}</a></CopyToClipboard>
      }, {
        key: 'role',
        dataIndex: 'role',
        title: '权限',
        align: 'center',
        render: text => text === 0 ? "超级管理员" : (text === 1 ? "一级代理" : "二级代理")
      }, {
        title: 'Action',
        key: 'action',
        align: 'center',
        render: (text, record, index) => <>
          <Button buttonContent="修改备注" onClick={() => this.setState({updateRemarksIndex: index})} />
          <Button mr={0} background="danger" disabled={record.role === 0} buttonContent="删除" onClick={() => this.deleteAccount(record._id)} />
        </>
      }
    ];
    return (
    	<div>
        <div className="container">
          <Search onChange={this.handleChangeSearchValue} value={searchValue} style={{width: 300, marginBottom: 20}} placeholder="请输入账户名" enterButton="查询" onSearch={this.searchAccount} />
          <Table rowKey={record => record._id} size="small" dataSource={accountList} columns={columns} />
        </div>
    	</div>
    )
  }
}

export default Account;