import React, {Component} from 'react';
import {Input, Table} from 'antd';
import {Link} from 'react-router-dom';
import { connect } from 'react-redux';
import Button from '../../../common/components/Button';
import {actionCreators} from './store';
import moment from 'moment';

const {Search} = Input;

class Template extends Component {
  render() {
    const {confName, templateMessageList, deleteTemplate, sendTemplate, changeConfNickName, searchConfName} = this.props;
    const templateColumns = [
      {
        key: "confName",
        dataIndex: "confName",
        title: "公众号",
        align: "center"
      }, {
        key: "name",
        dataIndex: "name",
        title: "消息名称",
        align: "center"
      }, {
        key: "sex",
        dataIndex: "sex",
        title: "消息名称",
        align: "center",
        render: text => text === "all" ? "全部" : (text === "1" ? "男" : (text === "2" ? "女" : "未知"))
      }, {
        key: "templateName",
        dataIndex: "templateName",
        title: "模板名称",
        align: "center"
      }, {
        key: "timing_time",
        dataIndex: "timing_time",
        title: "定时",
        align: "center",
        render: (text, record) => record.is_timing ? moment(text).format("YYYY-MM-DD HH:mm:ss") : "~~"
      }, {
        key: "Action",
        title: "操作",
        align: "center",
        width: 240,
        render: (text, record, index) => <div>
          <Button buttonContent={<Link to={{pathname: "./update", query: {...record}}}>修改</Link>} />
          {!record.is_timing && <Button buttonContent="发送" onClick={() => sendTemplate(record)} background="success"/>}
          <Button buttonContent="删除" onClick={() => deleteTemplate(record._id, index)} background="danger"/>
        </div>
      }
    ];
    return (
      <div className="container">
        <Search style={{width: 200}} placeholder="搜索公众号" value={confName} onChange={changeConfNickName} onSearch={searchConfName}/>
        <div className="fr"><Button size="default" className="fr" mb={20} buttonContent={<Link to="./create">+ 新建模板消息</Link>}/></div>
        <Table rowKey={record => record._id} size="middle" dataSource={templateMessageList} columns={templateColumns}/>
      </div>
    )
  }

  componentDidMount() {
    this.props.getTemplateList()
  }
}

const mapState = state => ({
  templateMessageList: state.message.template.templateMessageList,
  confName: state.message.template.confName,
});

const mapDispatch = dispatch => ({
  getTemplateList() {
    dispatch(actionCreators.getTemplateList())
  },
  deleteTemplate(id, index) {
    dispatch(actionCreators.deleteTemplate(id, index))
  },
  sendTemplate(record) {
    dispatch(actionCreators.sendTemplate(record))
  },
  changeConfNickName(e) {
    dispatch(actionCreators.changeConfNickName(e.target.value))
  },
  searchConfName(value) {
    dispatch(actionCreators.searchConfName(value))
  }
});

export default connect(mapState, mapDispatch)(Template);