import * as constants from './constants';

const initContent = {
  description: "",
  title: "",
  url: "",
  picurl: ""
};

const LocalDate = new Date(new Date().toLocaleDateString()).getTime();

const defaultState = {
  allMessageList: [],
  kefuMessageList: [],
  messageType: 4,
  messageGroup: "全部分组",
  selectedMessages: [],
  updateGroupModal: false,
  group: "",
  currentPage: 1,
  searchName: "",
  kefuMessageInfo: {
    contents: [],
    type: 0,
    img: "",
    img_path: "",
    channel: 1,
    sex: "all",
    timing_time: new Date(),
    daily_time: new Date(),
    delay: 5,
    tagId: "",
    tagName: "",
    codes: [],
    remarks: "",
    action_type: 1,
    isHour: 0,
    group: "未分组"
  },
  content: initContent,
  messageIndex: -1,
  paramsModal: false,
  allCodes: [],
  allGroup: [],
  selectGonghaoModal: true,
  previewModal: false,
  openid: "",
  groupInputVisible: false,
  groupValue: "",
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case constants.GET_KEFU_MESSAGE_LIST:
      newState.allMessageList = [...action.kefuMessageList];
      if(newState.messageGroup !== "全部分组") {
        newState.kefuMessageList = newState.allMessageList.filter(item => item.group === newState.messageGroup)
      } else if (newState.searchName) {
        newState.kefuMessageList = newState.allMessageList.filter(item => {
          let gonghaoList1 = [];
          item.gonghaoList.map(i => {
            i.nick_name === newState.searchName && gonghaoList1.push(i)
          });
          return gonghaoList1.length > 0
        })
      } else {
        newState.kefuMessageList = newState.allMessageList
      }
      return newState;
    case constants.CREATE_KEFU_MESSAGE:
      newState.kefuMessageList.unshift({...action.data});
      return newState;
    case constants.DELETE_LAST_MESSAGE:
      newState.kefuMessageList = [];
      return newState;
    case constants.DELETE_MESSAGE:
      newState.kefuMessageList.splice(action.index, 1);
      return newState;
    case constants.HANDLE_CHANGE_SELECT:
      newState.messageType = 4;
      newState.messageGroup = "全部分组";
      if(action.name === "messageType") {
        const { value: type } = action;
        newState.messageType = type;
        type === 0 && (newState.kefuMessageList = newState.allMessageList.filter(item => !(item.delay || item.is_timing || item.is_daily)));
        type === 1 && (newState.kefuMessageList = newState.allMessageList.filter(item => !!item.delay));
        type === 2 && (newState.kefuMessageList = newState.allMessageList.filter(item => !!item.is_timing));
        type === 3 && (newState.kefuMessageList = newState.allMessageList.filter(item => !!item.is_daily));
        type === 4 && (newState.kefuMessageList = newState.allMessageList.filter(item => !!item));
      } else {
        const { value: group } = action;
        newState.messageGroup = group;
        if( group === "全部分组") {
          newState.kefuMessageList = newState.allMessageList.filter(item => item.group !== "全部消息")
        } else {
          newState.kefuMessageList = newState.allMessageList.filter(item => item.group === group)
        }
      }
      if(newState.searchName) {
        newState.kefuMessageList = newState.kefuMessageList.filter(item => {
          let gonghaoList2 = [];
          item.gonghaoList.map(i => {
            i.nick_name === newState.searchName && gonghaoList2.push(i)
          });
          return gonghaoList2.length > 0
        })
      }
      return newState;
    case constants.CHANGE_SELECTED_MESSAGES:
      newState.selectedMessages = action.selectedMessages;
      return newState;
    case constants.OPEN_UPDATE_GROUP_MODAL:
      newState.updateGroupModal = true;
      return newState;
    case constants.CHANGE_GROUP:
      newState.group = action.value;
      return newState;
    case constants.CANCEL_UPDATE_GROUP:
      newState.updateGroupModal = false;
      newState.group = "";
      return newState;
    case constants.UPDATE_MORE_GROUP:
      newState.selectedMessages = [];
      return newState;
    case constants.CHANGE_SEARCH_NAME:
      newState.searchName = action.value;
      return newState;
    case constants.SEARCH_BY_NAME:
      let { searchName, allMessageList } = newState;
      newState.kefuMessageList = allMessageList.filter(item => {
        let gonghaoList = [];
        item.gonghaoList.map(i => {
          i.nick_name === searchName && gonghaoList.push(i)
        });
        return gonghaoList.length > 0
      });
      if(newState.kefuMessageList.length > 0) {
        action.message.success("查询成功");
      } else {
        newState.kefuMessageList = allMessageList;
        action.message.warning("没有查询到相关数据");
      }
      return newState;
    case constants.CLEAR_CONDITION:
      newState.group = newState.searchName = "";
      newState.messageGroup = "全部分组";
      newState.currentPage = 1;
      newState.kefuMessageList = newState.allMessageList;
      return newState;

    // 编辑客服消息
    case constants.CHANGE_CONTENT_VALUE:
      newState.content[action.name] = action.value;
      newState.kefuMessageInfo.contents.splice(newState.messageIndex, 1, newState.content);
      return newState;
    case constants.CHANGE_MESSAGE_VALUE:
      newState.kefuMessageInfo[action.name] = action.value;
      if(action.name === "type") {
        newState.content = initContent;
        newState.kefuMessageInfo.contents = [];
        newState.kefuMessageInfo.img = newState.kefuMessageInfo.img_path =  "";
        newState.messageIndex = -1;
        if(action.value === 0) {
          newState.kefuMessageInfo.contents.push(newState.content);
          newState.messageIndex = 0;
        }
      }
      if(action.name === "channel") {
        Object.assign(newState.kefuMessageInfo, {timing_time: new Date(), delay: 5, daily_time: new Date()})
      }
      return newState;
    case constants.ADD_MESSAGE:
      newState.content = initContent;
      newState.kefuMessageInfo.contents.push(newState.content);
      newState.messageIndex = newState.kefuMessageInfo.contents.length - 1;
      return newState;
    case constants.SELECT_MESSAGE:
      newState.content = {...action.info};
      newState.messageIndex = action.index;
      return newState;
    case constants.SELECT_PARAMS:
      newState.paramsModal = true;
      return newState;
    case constants.HIDE_PARAMS_MODAL:
      newState.paramsModal = false;
      return newState;
    case constants.GET_ALL_CODES:
      newState.allCodes = [...action.allCodes];
      return newState;
    case constants.CHANGE_GONGHAO:
      newState.kefuMessageInfo.codes = [...action.codes];
      return newState;
    case constants.OPEN_SELECT_MODAL:
      newState.selectGonghaoModal = true;
      return newState;
    case constants.CLOSE_SELECT_MODAL:
      newState.selectGonghaoModal = false;
      return newState;
    case constants.INIT_KEFU_MESSAGE_INFO:
      newState.kefuMessageInfo = {...action.query};
      newState.kefuMessageInfo.contents.length > 0 && (newState.content = {...newState.kefuMessageInfo.contents[0]});
      if(newState.kefuMessageInfo.type === 0) newState.messageIndex = 0;
      if(action.query.task) {
        newState.kefuMessageInfo.channel = 1
      } else if(action.query.is_timing) {
        newState.kefuMessageInfo.channel = 2
      } else if(action.query.is_daily) {
        newState.kefuMessageInfo.channel = 3;
        newState.kefuMessageInfo.daily_time += LocalDate
      } else {
        newState.kefuMessageInfo.channel = 0
      }
      newState.kefuMessageInfo.isHour && (newState.kefuMessageInfo.delay = newState.kefuMessageInfo.delay/60);
      !newState.kefuMessageInfo.isHour && (newState.kefuMessageInfo.isHour = 0);
      return newState;
    case constants.OPEN_PREVIEW_MODAL:
      newState.previewModal = true;
      return newState;
    case constants.CLOSE_PREVIEW_MODAL:
      newState.previewModal = false;
      newState.openid = "";
      return newState;
    case constants.CHANGE_OPENID:
      newState.openid = action.value;
      return newState;

    case constants.GET_ALL_GROUP:
      newState.allGroup = [...action.allGroup];
      return newState;
    case constants.SHOW_GROUP_INPUT:
      newState.groupInputVisible = true;
      setTimeout(function () {
        action.that.input.focus();
      }, 500);
      return newState;
    case constants.HANDLE_GROUP_INPUT_CHANGE:
      newState.groupValue = action.value;
      return newState;
    case constants.HANDLE_GROUP_INPUT_CONFIRM:
      Object.keys(action.data).length > 0 && newState.allGroup.push(action.data);
      newState.groupInputVisible = false;
      newState.groupValue = "";
      return newState;
    case constants.DELETE_GROUP:
      newState.allGroup.splice(action.index, 1);
      return newState;
    case constants.CANCEL_SELECT_GROUP:
      newState.kefuMessageInfo.group = "未分组";
      return newState;

    case constants.CHANGE_PAGE:
      newState.currentPage = action.page;
      return newState;

    case constants.RESET_STATE:
      let {currentPage, messageGroup, searchName: gonghaoName} = newState;
      newState = defaultState;
      newState.currentPage = currentPage;
      newState.messageGroup = messageGroup;
      newState.searchName = gonghaoName;
      return newState;
    default:
      return state;
  }
};